import BaseLog from '@baidu/feed-baselog';
import {getUrlParam} from '@/lib/utils/common';

const initArgs = {
    params: {
        from: 'feed',
        page: 'tiaozhan_publish_landing',
    },
};

const baselog = new BaseLog({
    ...initArgs,
});

const source = getUrlParam('source') || 'all';

const commArgs = {
    ubcId: '17003',
    params: {
        source,
    },
};

export function sendShowLog({value, ext}) {
    baselog.sendUbc({
        ...commArgs,
        params: {
            ...commArgs.params,
            type: 'show',
            value,
            ext,
        },
    });
}

export function sendClickLog({value, ext}) {
    baselog.sendUbc({
        ...commArgs,
        params: {
            ...commArgs.params,
            type: 'click',
            value,
            ext,
        },
    });
}
