exports.push([module.id, "/**\n * @file 组件样式 折叠面板\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-collapse-item {\n  position: relative;\n}\n.s-collapse-item-header .s-cell-arrow {\n  transition: -webkit-transform 0.3s linear;\n  transition: transform 0.3s linear;\n  transition: transform 0.3s linear, -webkit-transform 0.3s linear;\n}\n.s-collapse-item-header-disabled {\n  opacity: 0.2;\n}\n.s-collapse-item-panel {\n  overflow: hidden;\n  will-change: height;\n  transition: height 0.3s ease-in-out;\n}\n.s-collapse-item-content {\n  padding: 0.08rem 0.17rem;\n  font-size: 0.13rem;\n  color: rgba(132, 134, 145, 1);\n}\n.darkmode .s-collapse-item-content {\n  color: rgba(255, 255, 255, 0.5);\n}\n.s-collapse-item-hairline {\n  position: absolute;\n  bottom: 0;\n  width: 100%;\n  height: 1px;\n  position: relative;\n}\n.nightmode .s-collapse-item-hairline:after {\n  border-color: rgba(0, 0, 0, 0.9);\n  background-color: rgba(0, 0, 0, 0.01);\n  opacity: 0.3;\n}\n.s-collapse-item-hairline,\n.s-collapse-item-hairline:after {\n  border-bottom: 1px solid;\n  border-color: rgba(238, 238, 238, 1);\n}\n.darkmode .s-collapse-item-hairline,\n.darkmode .s-collapse-item-hairline:after {\n  border-color: rgba(102, 102, 102, 0.4);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 150%;\n    height: 150%;\n    -webkit-transform: scale(0.66666667);\n            transform: scale(0.66666667);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 200%;\n    height: 200%;\n    -webkit-transform: scale(0.5);\n            transform: scale(0.5);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 300%;\n    height: 300%;\n    -webkit-transform: scale(0.33333333);\n            transform: scale(0.33333333);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n.s-collapse-item:last-child .s-collapse-item-hairline {\n  display: none;\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/collapse/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/theme/_theme.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECkBG,kBAAA;AFWJ;AC7BC;ECqBO,yCAAA;EAAA,iCAAA;EAAA,gEAAA;AFWR;AERI;EACI,YAAA;AFUR;AEPI;EACI,gBAAA;EACA,mBAAA;EACA,mCAAA;AFSR;AENI;EACI,wBAAA;EACA,kBAAA;EC+FJ,6BAAA;AHtFJ;AC7CC;EE0Ie,+BAAA;AH1FhB;AERI;EACI,kBAAA;EACA,SAAA;EACA,WAAA;EACA,WAAA;EEuFJ,kBAAA;AJ5EJ;ACvDC;EGgEO,gCAAA;EACA,qCAAA;EACA,YAAA;AJNR;AI0EI;;EAEI,wBAAA;EDhEJ,oCAAA;AHPJ;ACjEC;;EE+Ee,sCAAA;AHVhB;AIuEI;EAAA;IApEA,YAAA;EJCF;EICE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,oCAAA;YAAA,4BAAA;IACA,6BAAA;YAAA,qBAAA;EJCN;AACF;AI8DI;EAAA;IA3EA,YAAA;EJiBF;EIfE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,6BAAA;YAAA,qBAAA;IACA,6BAAA;YAAA,qBAAA;EJiBN;AACF;AImDI;EAAA;IAhFA,YAAA;EJiCF;EI/BE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,oCAAA;YAAA,4BAAA;IACA,6BAAA;YAAA,qBAAA;EJiCN;AACF;AErEI;EACI,aAAA;AFuER","file":"index.less","sourcesContent":["/**\n * @file 组件样式 折叠面板\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-collapse-item {\n  position: relative;\n}\n.s-collapse-item-header .s-cell-arrow {\n  transition: transform 0.3s linear;\n}\n.s-collapse-item-header-disabled {\n  opacity: 0.2;\n}\n.s-collapse-item-panel {\n  overflow: hidden;\n  will-change: height;\n  transition: height 0.3s ease-in-out;\n}\n.s-collapse-item-content {\n  padding: 24pr 51pr;\n  font-size: 39pr;\n  color: rgba(132, 134, 145, 1);\n}\n.darkmode .s-collapse-item-content {\n  color: rgba(255, 255, 255, 0.5);\n}\n.s-collapse-item-hairline {\n  position: absolute;\n  bottom: 0;\n  width: 100%;\n  height: 1px;\n  position: relative;\n}\n.nightmode .s-collapse-item-hairline:after {\n  border-color: rgba(0, 0, 0, 0.9);\n  background-color: rgba(0, 0, 0, 0.01);\n  opacity: 0.3;\n}\n.s-collapse-item-hairline,\n.s-collapse-item-hairline:after {\n  border-bottom: 1px solid;\n  border-color: rgba(238, 238, 238, 1);\n}\n.darkmode .s-collapse-item-hairline,\n.darkmode .s-collapse-item-hairline:after {\n  border-color: rgba(102, 102, 102, 0.4);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 150%;\n    height: 150%;\n    transform: scale(0.66666667);\n    transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 200%;\n    height: 200%;\n    transform: scale(0.5);\n    transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .s-collapse-item-hairline {\n    border: none;\n  }\n  .s-collapse-item-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 300%;\n    height: 300%;\n    transform: scale(0.33333333);\n    transform-origin: 0 0;\n  }\n}\n.s-collapse-item:last-child .s-collapse-item-hairline {\n  display: none;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 折叠面板\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-cls: s-collapse;\n@item-cls: s-collapse-item;\n@cell-cls: s-cell;\n\n// .@{prefix-cls} {\n//     border-style: solid;\n//     border-width: @P1 0;\n//     .theme-border(GC33);\n// }\n\n.@{item-cls} {\n    position: relative;\n\n    &-header .@{cell-cls}-arrow {\n        transition: transform 0.3s linear;\n    }\n\n    &-header-disabled {\n        opacity: 0.2;\n    }\n\n    &-panel {\n        overflow: hidden;\n        will-change: height;\n        transition: height 0.3s ease-in-out;\n    }\n\n    &-content {\n        padding: @P0 @M;\n        font-size: 39pr;\n        .theme-color(GC4);\n    }\n\n    &-hairline {\n        position: absolute;\n        bottom: 0;\n        width: 100%;\n        height: 1px;\n        .theme-hairline(bottom, GC34);\n    }\n\n    &:last-child &-hairline {\n        display: none;\n    }\n}\n","/**\n * @file color mixins\n * @author wuji.baidu-int.com\n *\n * 区别于color、background-color、border-color等受全局dark-mode和night-mode变量影响。\n * fill属性可以定义局部变量night-mode，这在关闭夜间模式情况下，可以单独保留svg填充颜色fill属性。\n * 究其原因：百度APP安卓夜间模式下，基本的颜色属性不能通过css定义生效，svg的颜色属性例外。建议自行测试验证和理解。\n */\n\n// TODO: 使用css变量\n// .use-cssvar() {}\n\n// mixin: 生成背景颜色\n.wuji-background-color(@code: undefined) {\n    @color-default: getColor(@code);\n    background-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                background-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                background-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            background-color: @color-default;\n        }\n    }\n}\n\n// mixin: 添加区分模式的背景图片(可置于全局mixin中)\n.wuji-background-image(@src: null, @src-dark: null, @src-night: null) {\n    & when not (@src = null) {\n        background-image: url(@src);\n    }\n    & when (@multi-mode = true) {\n        & when not (@src-dark = null) and (@dark-mode = true) and not (@src = @src-dark) {\n            @{selector-dark} & {\n                background-image: url(@src-dark);\n            }\n        }\n        & when not (@src-night = null) and (@night-mode = true) and not (@src = @src-night) {\n            @{selector-night} & {\n                background-image: url(@src-night);\n            }\n        }\n    }\n}\n\n// mixin: 生成背景颜色\n.wuji-background(@code: undefined) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成描边颜色\n.wuji-border-color(@code: undefined, @position: around) {\n    @color-default: getColor(@code);\n    @prop: if(@position = around, e('border-color'), e('border-@{position}-color'));\n    @{prop}: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                @{prop}: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                @{prop}: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            @{prop}: @color-default;\n        }\n    }\n}\n\n// mixin: 生成描边颜色\n// 当第一个参数是多值情况且固定顺序 <line-width> <line-style> <color> 时\n// 会调用line/mixin.less中的wuji-border方法，参数示例：1px solid red\n.wuji-border(@code: undefined, @position: around) when (length(@code) = 1) {\n    .wuji-border-color(@code, @position);\n}\n\n// mixn: 生成上描边颜色\n.wuji-border-top-color(@code: undefined) {\n    .wuji-border-color(@code, top);\n}\n\n// mixn: 生成下描边颜色\n.wuji-border-bottom-color(@code: undefined) {\n    .wuji-border-color(@code, bottom);\n}\n\n// mixn: 生成左描边颜色\n.wuji-border-left-color(@code: undefined) {\n    .wuji-border-color(@code, left);\n}\n\n// mixn: 生成右描边颜色\n.wuji-border-right-color(@code: undefined) {\n    .wuji-border-color(@code, right);\n}\n\n// mixin: 生成文字颜色\n.wuji-color(@code: undefined) {\n    @color-default: getColor(@code);\n    color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成svg填充颜色\n.wuji-fill(@code: undefined, @night-mode: true) {\n    @color-default: getColor(@code);\n    fill: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                fill: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                fill: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            fill: @color-default;\n        }\n    }\n}\n\n// mixin: 生成光标颜色\n.wuji-caret(@code: undefined) {\n    @color-default: getColor(@code);\n    caret-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                caret-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                caret-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            caret-color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成透明度\n.wuji-opacity(@code: undefined) {\n    opacity: getOpacity(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.wuji-root(\n    @color: CAM_X791, // GC1\n    @background: CAM_X392, // GC9\n    @selector-root: @selector-root,\n    @selector-dark: @selector-dark,\n    @selector-night: @selector-night\n) {\n    // 如果选择器参数是字符串类型，需要escape\n    @rule1: if(isstring(@selector-root), e(@selector-root), @selector-root);\n    @rule2: if(isstring(@selector-dark), e(@selector-dark), @selector-dark);\n    @rule3: if(isstring(@selector-night), e(@selector-night), @selector-night);\n\n    @{rule1} {\n        color: getColor(@color);\n        background-color: getColor(@background);\n\n        & when (@multi-mode = true) and (@dark-mode = true) {\n            &@{rule2} {\n                color: getColor(@color, Dark);\n                background-color: getColor(@background, Dark);\n            }\n        }\n\n        & when (@multi-mode = true) and (@night-mode = true) {\n            &@{rule3} {\n                color: getColor(@color, Night);\n                background-color: getColor(@background, Night);\n            }\n        }\n    }\n}\n","/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n\n// 是否使用原生CSS变量，默认false\n@use-cssvar: false;\n// 是否支持多种模式：默认模式，暗黑模式，夜间模式；false 时只支持默认模式\n@multi-mode: true;\n// 是否支持暗黑模式, @multi-mode 为 true 时有效\n@dark-mode: true;\n// 是否支持夜间模式, @multi-mode 为 true 时有效\n@night-mode: false;\n// 主题颜色全局选择器，定义在根元素上，默认根元素html，可通过赋值改变\n@selector-root: html;\n// 日间模式 类选择器 可用于暗黑模式下部分区域依旧需要保持日间模式的情况\n@selector-default: '';\n// 深色模式 类选择器\n@selector-dark: .darkmode;\n// 夜间模式 类选择器\n@selector-night: .nightmode;\n\n// 主题图标路径\n@image-path: '../assets/img';\n\n.use-cssvar() when(@use-cssvar = true) {\n}\n\n// mixin: 生成文字颜色\n.theme-color(@code) when (@use-cssvar = true) {\n}\n.theme-color(@code) when (@use-cssvar = false) {\n    .wuji-color(@code);\n}\n\n// mixin: 生成svg填充颜色\n.theme-fill(@code) when (@use-cssvar = true) {\n}\n.theme-fill(@code) when (@use-cssvar = false) {\n    .wuji-fill(@code);\n}\n\n// mixin: 生成背景颜色\n.theme-background(@code) when (@use-cssvar = true) {\n}\n.theme-background(@code) when (@use-cssvar = false) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成边框颜色\n.theme-border(@code) when (@use-cssvar = true) {\n}\n.theme-border(@code) when (@use-cssvar = false) {\n    .wuji-border-color(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.theme-root() {\n    .wuji-root();\n}\n\n// hack: 安卓夜间模式1px看不清，通过在伪元素:after上定义rgba颜色后再微调透明度\n.hairline-nightmode() {\n    @{selector-night} &:after {\n        border-color: rgba(0,0,0,0.9);\n        background-color: rgba(0,0,0,0.01);\n        opacity: .3;\n    }\n}\n\n// 根据media query缩放，dpr: 1.5、2、3\n.hairline-media(@dpr, @position) {\n    border: none;\n\n    &:after {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        pointer-events: none;\n        width: @dpr * 100%;\n        height: @dpr * 100%;\n        transform: scale(1 / @dpr);\n        transform-origin: 0 0;\n    }\n}\n\n// mixin: 使用色表编号的1px边框，不支持自定义颜色，自定义颜色可使用.hairline(...)\n.theme-hairline(@position, @code: GC34, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border-radius: @radius;\n    .theme-border(@code);\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.theme-hairline(@position: bottom, @code: GC34) when not (@position = around) {\n    position: relative;\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border-@{position}: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n    }\n}\n\n// mixin: 不同主题下背景图路径\n.theme-image(@image, @light: true, @night: true, @dark: true, @base64: false) {\n    @output4light: if(@light = true, {\n        @img: '@{image-path}/@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        background-image: @url;\n    }, {});\n\n    @output4dark: if(@dark = true, {\n        @img: '@{image-path}/darkmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-dark} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4night: if(@night = true, {\n        @img: '@{image-path}/nightmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-night} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4light();\n\n    @output4dark();\n\n    @output4night();\n}\n"]}]);
