/**
 * @file 视频专区组件
 */

import {Component} from 'san';
import {invokeScheme} from '@/lib/utils/common';
import {sendShowLog, sendClickLog} from '../../log';
import './index.less';

export default class App extends Component {
    static template = /* html */ `
        <div class="zenoWrap">
            <div class="containerWrap">
                <div class="themeWrap" s-for="item, index in data">
                    <div class="themeHead">
                        <div class="talkIcon"></div>
                        <span class="themeLabel">{{item.topic[0].label}}</span>
                        <div class="pubBtnIcon" on-click="pubBtnClick(item.topic[0])">
                            {{btnText}}
                            <div s-if="{{index === 0 && firstFingerClick}}" class="fingerPointIcon"></div>
                        </div>
                        <div s-if="{{index === 0 && firstFingerClick}}" class="fingerIcon"></div>
                    </div>

                    <div class="themelist">
                        <div class="themeItemWrap" s-for="itemList, index in item.topic[0].list" on-click="zenoClick(item.topic[0], itemList)">
                            <div class="themeItem" style="background: url({{itemList.image}}) no-repeat center/cover;"></div>
                            <span class="themeText">{{itemList.title}}</span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `;

    static computed = {
    };

    zenoClick(item, itemList) {
        // console.log(item.label);
        // console.log(itemList.nid);
        sendClickLog({
            value: 'theme_video_demo',
            ext: {
                topic: item.label,
                nid: itemList.nid,
            },
        });
        if(itemList?.cmd) {
            invokeScheme(itemList.cmd);
        };
    };

    pubBtnClick(item) {
        // console.log(item.label);
        // console.log(item.button?.cmd);
        sendClickLog({
            value: 'theme_go_publish',
            ext: {
                topic: item.label,
            },
        });
        this.fire('pubBtnClick', {cmd: item.button && item.button?.cmd});
    }

    initData() {
        return {
            btnText: '拍同款',
        };
    };

    attached() {
    };
}
