exports.push([module.id, "/**\n * @file 组件样式 侧边栏\n * @author xueruijun <xueruijun@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-sidebar {\n  position: fixed;\n  top: 0.44rem;\n  bottom: 0;\n  width: 0.74rem;\n  color: rgba(132, 134, 145, 1);\n  background-color: rgba(245, 246, 249, 1);\n  font-size: 0.15rem;\n  overflow: hidden;\n  overflow-y: auto;\n  z-index: 9;\n}\n.darkmode .s-sidebar {\n  color: rgba(255, 255, 255, 0.5);\n}\n.darkmode .s-sidebar {\n  background-color: rgba(68, 68, 68, 0.5);\n}\n.s-sidebar .name {\n  display: -webkit-flex;\n  display: flex;\n  -webkit-justify-content: center;\n          justify-content: center;\n  -webkit-align-items: center;\n          align-items: center;\n  position: relative;\n  height: 0.8rem;\n  overflow-x: hidden;\n}\n.s-sidebar .selected {\n  color: rgba(30, 31, 36, 1);\n  width: 0.75rem;\n  letter-spacing: 0;\n  background-color: rgba(255, 255, 255, 1);\n  font-size: 0.18rem;\n  font-weight: 700;\n}\n.darkmode .s-sidebar .selected {\n  color: rgba(255, 255, 255, 0.8);\n}\n.darkmode .s-sidebar .selected {\n  background-color: rgba(8, 8, 8, 1);\n}\n.s-sidebar .selected:before {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 50%;\n  margin-top: -0.1rem;\n  width: 0.04rem;\n  height: 0.2rem;\n  background-color: rgba(78, 110, 242, 1);\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/sidebar/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECUG,eAAA;EACA,YAAA;EACA,SAAA;EACA,cAAA;ECsHA,6BAAA;EApHA,wCAAA;EDCA,kBAAA;EACA,gBAAA;EACA,gBAAA;EACA,UAAA;AFmBJ;ACtCC;EE0Ie,+BAAA;AHjGhB;ACzCC;EEsBe,uCAAA;AHsBhB;AC5CC;ECsBO,qBAAA;EAAA,aAAA;EACA,+BAAA;UAAA,uBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,kBAAA;EACA,cAAA;EACA,kBAAA;AFyBR;ACpDC;EEmIG,0BAAA;EDnGI,cAAA;EACA,iBAAA;EClBJ,wCAAA;EDoBI,kBAAA;EACA,gBAAA;AFwBR;AC5DC;EE0Ie,+BAAA;AH3EhB;AC/DC;EEsBe,kCAAA;AH4ChB;AE5BQ;EACI,WAAA;EACA,kBAAA;EACA,OAAA;EACA,QAAA;EACA,mBAAA;EACA,cAAA;EACA,cAAA;EC9BR,uCAAA;AH6DJ","file":"index.less","sourcesContent":["/**\n * @file 组件样式 侧边栏\n * @author xueruijun <xueruijun@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-sidebar {\n  position: fixed;\n  top: 132pr;\n  bottom: 0;\n  width: 222pr;\n  color: rgba(132, 134, 145, 1);\n  background-color: rgba(245, 246, 249, 1);\n  font-size: 45pr;\n  overflow: hidden;\n  overflow-y: auto;\n  z-index: 9;\n}\n.darkmode .s-sidebar {\n  color: rgba(255, 255, 255, 0.5);\n}\n.darkmode .s-sidebar {\n  background-color: rgba(68, 68, 68, 0.5);\n}\n.s-sidebar .name {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  position: relative;\n  height: 240pr;\n  overflow-x: hidden;\n}\n.s-sidebar .selected {\n  color: rgba(30, 31, 36, 1);\n  width: 225pr;\n  letter-spacing: 0;\n  background-color: rgba(255, 255, 255, 1);\n  font-size: 54pr;\n  font-weight: 700;\n}\n.darkmode .s-sidebar .selected {\n  color: rgba(255, 255, 255, 0.8);\n}\n.darkmode .s-sidebar .selected {\n  background-color: rgba(8, 8, 8, 1);\n}\n.s-sidebar .selected:before {\n  content: '';\n  position: absolute;\n  left: 0;\n  top: 50%;\n  margin-top: -30pr;\n  width: 12pr;\n  height: 60pr;\n  background-color: rgba(78, 110, 242, 1);\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 侧边栏\n * @author xueruijun <xueruijun@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-cls: s-sidebar;\n\n.@{prefix-cls} {\n    position: fixed;\n    top: 132pr;\n    bottom: 0;\n    width: 222pr;\n    .theme-color(GC4);\n    .theme-background(IC46);\n    font-size: 45pr;\n    overflow: hidden;\n    overflow-y: auto;\n    z-index: 9;\n\n    .name {\n        display: flex;\n        justify-content: center;\n        align-items: center;\n        position: relative;\n        height: 240pr;\n        overflow-x: hidden;\n    }\n\n    .selected {\n        .theme-color(GC2);\n        width: 225pr;\n        letter-spacing: 0;\n        .theme-background(GC9);\n        font-size: 54pr;\n        font-weight: 700;\n\n        &:before {\n            content: '';\n            position: absolute;\n            left: 0;\n            top: 50%;\n            margin-top: -30pr;\n            width: 12pr;\n            height: 60pr;\n            .theme-background(GC7);\n        }\n    }\n}\n","/**\n * @file color mixins\n * @author wuji.baidu-int.com\n *\n * 区别于color、background-color、border-color等受全局dark-mode和night-mode变量影响。\n * fill属性可以定义局部变量night-mode，这在关闭夜间模式情况下，可以单独保留svg填充颜色fill属性。\n * 究其原因：百度APP安卓夜间模式下，基本的颜色属性不能通过css定义生效，svg的颜色属性例外。建议自行测试验证和理解。\n */\n\n// TODO: 使用css变量\n// .use-cssvar() {}\n\n// mixin: 生成背景颜色\n.wuji-background-color(@code: undefined) {\n    @color-default: getColor(@code);\n    background-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                background-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                background-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            background-color: @color-default;\n        }\n    }\n}\n\n// mixin: 添加区分模式的背景图片(可置于全局mixin中)\n.wuji-background-image(@src: null, @src-dark: null, @src-night: null) {\n    & when not (@src = null) {\n        background-image: url(@src);\n    }\n    & when (@multi-mode = true) {\n        & when not (@src-dark = null) and (@dark-mode = true) and not (@src = @src-dark) {\n            @{selector-dark} & {\n                background-image: url(@src-dark);\n            }\n        }\n        & when not (@src-night = null) and (@night-mode = true) and not (@src = @src-night) {\n            @{selector-night} & {\n                background-image: url(@src-night);\n            }\n        }\n    }\n}\n\n// mixin: 生成背景颜色\n.wuji-background(@code: undefined) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成描边颜色\n.wuji-border-color(@code: undefined, @position: around) {\n    @color-default: getColor(@code);\n    @prop: if(@position = around, e('border-color'), e('border-@{position}-color'));\n    @{prop}: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                @{prop}: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                @{prop}: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            @{prop}: @color-default;\n        }\n    }\n}\n\n// mixin: 生成描边颜色\n// 当第一个参数是多值情况且固定顺序 <line-width> <line-style> <color> 时\n// 会调用line/mixin.less中的wuji-border方法，参数示例：1px solid red\n.wuji-border(@code: undefined, @position: around) when (length(@code) = 1) {\n    .wuji-border-color(@code, @position);\n}\n\n// mixn: 生成上描边颜色\n.wuji-border-top-color(@code: undefined) {\n    .wuji-border-color(@code, top);\n}\n\n// mixn: 生成下描边颜色\n.wuji-border-bottom-color(@code: undefined) {\n    .wuji-border-color(@code, bottom);\n}\n\n// mixn: 生成左描边颜色\n.wuji-border-left-color(@code: undefined) {\n    .wuji-border-color(@code, left);\n}\n\n// mixn: 生成右描边颜色\n.wuji-border-right-color(@code: undefined) {\n    .wuji-border-color(@code, right);\n}\n\n// mixin: 生成文字颜色\n.wuji-color(@code: undefined) {\n    @color-default: getColor(@code);\n    color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成svg填充颜色\n.wuji-fill(@code: undefined, @night-mode: true) {\n    @color-default: getColor(@code);\n    fill: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                fill: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                fill: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            fill: @color-default;\n        }\n    }\n}\n\n// mixin: 生成光标颜色\n.wuji-caret(@code: undefined) {\n    @color-default: getColor(@code);\n    caret-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                caret-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                caret-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            caret-color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成透明度\n.wuji-opacity(@code: undefined) {\n    opacity: getOpacity(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.wuji-root(\n    @color: CAM_X791, // GC1\n    @background: CAM_X392, // GC9\n    @selector-root: @selector-root,\n    @selector-dark: @selector-dark,\n    @selector-night: @selector-night\n) {\n    // 如果选择器参数是字符串类型，需要escape\n    @rule1: if(isstring(@selector-root), e(@selector-root), @selector-root);\n    @rule2: if(isstring(@selector-dark), e(@selector-dark), @selector-dark);\n    @rule3: if(isstring(@selector-night), e(@selector-night), @selector-night);\n\n    @{rule1} {\n        color: getColor(@color);\n        background-color: getColor(@background);\n\n        & when (@multi-mode = true) and (@dark-mode = true) {\n            &@{rule2} {\n                color: getColor(@color, Dark);\n                background-color: getColor(@background, Dark);\n            }\n        }\n\n        & when (@multi-mode = true) and (@night-mode = true) {\n            &@{rule3} {\n                color: getColor(@color, Night);\n                background-color: getColor(@background, Night);\n            }\n        }\n    }\n}\n"]}]);
