exports.push([module.id, "/**\n * @file 组件样式 轮播图\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-carousel {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  display: -webkit-flex;\n  display: flex;\n  white-space: nowrap;\n  opacity: 1;\n  z-index: 1;\n}\n.s-carousel-wrap {\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n  padding-top: 40.09661836%;\n  background-color: rgba(245, 246, 249, 1);\n}\n.darkmode .s-carousel-wrap {\n  background-color: rgba(44, 44, 44, 1);\n}\n.s-carousel-item {\n  display: inline-block;\n  -webkit-flex: 0 0 auto;\n          flex: 0 0 auto;\n  width: 100%;\n  height: 100%;\n}\n.s-carousel-info {\n  display: -webkit-flex;\n  display: flex;\n  -webkit-justify-content: center;\n          justify-content: center;\n  position: absolute;\n  bottom: 0;\n  padding: 0 0.08rem 0.1rem 0.17rem;\n  width: 100%;\n  z-index: 2;\n  color: #fff;\n}\n.s-carousel-info.align-left {\n  -webkit-justify-content: flex-start;\n          justify-content: flex-start;\n}\n.s-carousel-info.align-right {\n  -webkit-justify-content: flex-end;\n          justify-content: flex-end;\n}\n.s-carousel-with-title {\n  -webkit-align-items: center;\n          align-items: center;\n  padding-bottom: 0;\n  height: 0.38rem;\n}\n.s-carousel-mask {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 100%);\n}\n.s-carousel-title {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  min-width: 0;\n  position: relative;\n  padding-right: 1em;\n  width: 100%;\n  font-size: 0.14rem;\n}\n.s-carousel-indicator {\n  position: relative;\n  display: -webkit-flex;\n  display: flex;\n  -webkit-align-items: center;\n          align-items: center;\n  -webkit-flex-shrink: 0;\n          flex-shrink: 0;\n}\n.s-carousel-indicator-num {\n  border-radius: 99px;\n  padding: 0 0.08rem;\n  font-size: 10px;\n  height: 0.2rem;\n  background-color: rgba(0, 0, 0, 0.4);\n}\n.s-carousel-indicator-dot .dot {\n  display: block;\n  margin: 0 3px;\n  border-radius: 100%;\n  width: 6px;\n  height: 6px;\n  background-color: #c8c8c8;\n}\n.s-carousel-indicator-dot .active {\n  background-color: #fff;\n}\n.s-carousel-animate {\n  transition: all 0.3s ease;\n}\n.s-carousel-loop .s-carousel-item:first-child {\n  margin-left: -100%;\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/carousel/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/style/mixins.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECUG,kBAAA;EACA,MAAA;EACA,QAAA;EACA,SAAA;EACA,OAAA;EACA,qBAAA;EAAA,aAAA;EACA,mBAAA;EACA,UAAA;EACA,UAAA;AFmBJ;AEjBI;EACI,kBAAA;EACA,gBAAA;EACA,UAAA;EACA,yBAAA;ECTJ,wCAAA;AH6BJ;AC5CC;EEsBe,qCAAA;AHyBhB;AEnBI;EACI,qBAAA;EACA,sBAAA;UAAA,cAAA;EACA,WAAA;EACA,YAAA;AFqBR;AElBI;EACI,qBAAA;EAAA,aAAA;EACA,+BAAA;UAAA,uBAAA;EACA,kBAAA;EACA,SAAA;EACA,iCAAA;EACA,WAAA;EACA,UAAA;EACA,WAAA;AFoBR;AElBQ;EACI,mCAAA;UAAA,2BAAA;AFoBZ;AEjBQ;EACI,iCAAA;UAAA,yBAAA;AFmBZ;AEfI;EACI,2BAAA;UAAA,mBAAA;EACA,iBAAA;EACA,eAAA;AFiBR;AEdI;EACI,kBAAA;EACA,MAAA;EACA,QAAA;EACA,SAAA;EACA,OAAA;EACA,yFAAA;AFgBR;AEbI;EEoHA,gBAAA;EACA,uBAAA;EACA,mBAAA;EACA,YAAA;EFrHI,kBAAA;EACA,kBAAA;EACA,WAAA;EACA,kBAAA;AFkBR;AEfI;EACI,kBAAA;EACA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,sBAAA;UAAA,cAAA;AFiBR;AEfQ;EAEI,mBAAA;EACA,kBAAA;EACA,eAAA;EACA,cAAA;EACA,oCAAA;AFgBZ;AEbQ;EAEQ,cAAA;EACA,aAAA;EACA,mBAAA;EACA,UAAA;EACA,WAAA;EACA,yBAAA;AFchB;AErBQ;EAWQ,sBAAA;AFahB;AERI;EACI,yBAAA;AFUR;AEPI;EACI,kBAAA;AFSR","file":"index.less","sourcesContent":["/**\n * @file 组件样式 轮播图\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-carousel {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  display: flex;\n  white-space: nowrap;\n  opacity: 1;\n  z-index: 1;\n}\n.s-carousel-wrap {\n  position: relative;\n  overflow: hidden;\n  z-index: 1;\n  padding-top: 40.09661836%;\n  background-color: rgba(245, 246, 249, 1);\n}\n.darkmode .s-carousel-wrap {\n  background-color: rgba(44, 44, 44, 1);\n}\n.s-carousel-item {\n  display: inline-block;\n  flex: 0 0 auto;\n  width: 100%;\n  height: 100%;\n}\n.s-carousel-info {\n  display: flex;\n  justify-content: center;\n  position: absolute;\n  bottom: 0;\n  padding: 0 24pr 30pr 51pr;\n  width: 100%;\n  z-index: 2;\n  color: #fff;\n}\n.s-carousel-info.align-left {\n  justify-content: flex-start;\n}\n.s-carousel-info.align-right {\n  justify-content: flex-end;\n}\n.s-carousel-with-title {\n  align-items: center;\n  padding-bottom: 0;\n  height: 114pr;\n}\n.s-carousel-mask {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 100%);\n}\n.s-carousel-title {\n  overflow: hidden;\n  text-overflow: ellipsis;\n  white-space: nowrap;\n  min-width: 0;\n  position: relative;\n  padding-right: 1em;\n  width: 100%;\n  font-size: 42pr;\n}\n.s-carousel-indicator {\n  position: relative;\n  display: flex;\n  align-items: center;\n  flex-shrink: 0;\n}\n.s-carousel-indicator-num {\n  border-radius: 99px;\n  padding: 0 24pr;\n  font-size: 10px;\n  height: 60pr;\n  background-color: rgba(0, 0, 0, 0.4);\n}\n.s-carousel-indicator-dot .dot {\n  display: block;\n  margin: 0 3px;\n  border-radius: 100%;\n  width: 6px;\n  height: 6px;\n  background-color: #c8c8c8;\n}\n.s-carousel-indicator-dot .active {\n  background-color: #fff;\n}\n.s-carousel-animate {\n  transition: all 0.3s ease;\n}\n.s-carousel-loop .s-carousel-item:first-child {\n  margin-left: -100%;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 轮播图\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-cls: s-carousel;\n\n.@{prefix-cls} {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    display: flex;\n    white-space: nowrap;\n    opacity: 1;\n    z-index: 1;\n\n    &-wrap {\n        position: relative;\n        overflow: hidden;\n        z-index: 1;\n        padding-top: 498 / 1242 * 100%; // 固定宽高比 height: 498pr;\n        .theme-background(GC57);\n    }\n\n    &-item {\n        display: inline-block;\n        flex: 0 0 auto;\n        width: 100%;\n        height: 100%;\n    }\n\n    &-info {\n        display: flex;\n        justify-content: center;\n        position: absolute;\n        bottom: 0;\n        padding: 0 24pr 30pr 51pr;\n        width: 100%;\n        z-index: 2;\n        color: #fff;\n\n        &.align-left {\n            justify-content: flex-start;\n        }\n\n        &.align-right {\n            justify-content: flex-end;\n        }\n    }\n\n    &-with-title {\n        align-items: center;\n        padding-bottom: 0;\n        height: 114pr;\n    }\n\n    &-mask {\n        position: absolute;\n        top: 0;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        background-image: linear-gradient(-180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.45) 100%);\n    }\n\n    &-title {\n        .ellipsis();\n        position: relative;\n        padding-right: 1em;\n        width: 100%;\n        font-size: 42pr;\n    }\n\n    &-indicator {\n        position: relative;\n        display: flex;\n        align-items: center;\n        flex-shrink: 0;\n\n        &-num {\n            // .centerfy();\n            border-radius: 99px;\n            padding: 0 24pr;\n            font-size: 10px;\n            height: 60pr;\n            background-color: rgba(0, 0, 0, 0.4);\n        }\n\n        &-dot {\n            .dot {\n                display: block;\n                margin: 0 3px;\n                border-radius: 100%;\n                width: 6px;\n                height: 6px;\n                background-color: #c8c8c8;\n            }\n\n            .active {\n                background-color: #fff;\n            }\n        }\n    }\n\n    &-animate {\n        transition: all 0.3s ease;\n    }\n\n    &-loop &-item:first-child {\n        margin-left: -100%;\n    }\n}\n","/**\n * @file color mixins\n * @author wuji.baidu-int.com\n *\n * 区别于color、background-color、border-color等受全局dark-mode和night-mode变量影响。\n * fill属性可以定义局部变量night-mode，这在关闭夜间模式情况下，可以单独保留svg填充颜色fill属性。\n * 究其原因：百度APP安卓夜间模式下，基本的颜色属性不能通过css定义生效，svg的颜色属性例外。建议自行测试验证和理解。\n */\n\n// TODO: 使用css变量\n// .use-cssvar() {}\n\n// mixin: 生成背景颜色\n.wuji-background-color(@code: undefined) {\n    @color-default: getColor(@code);\n    background-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                background-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                background-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            background-color: @color-default;\n        }\n    }\n}\n\n// mixin: 添加区分模式的背景图片(可置于全局mixin中)\n.wuji-background-image(@src: null, @src-dark: null, @src-night: null) {\n    & when not (@src = null) {\n        background-image: url(@src);\n    }\n    & when (@multi-mode = true) {\n        & when not (@src-dark = null) and (@dark-mode = true) and not (@src = @src-dark) {\n            @{selector-dark} & {\n                background-image: url(@src-dark);\n            }\n        }\n        & when not (@src-night = null) and (@night-mode = true) and not (@src = @src-night) {\n            @{selector-night} & {\n                background-image: url(@src-night);\n            }\n        }\n    }\n}\n\n// mixin: 生成背景颜色\n.wuji-background(@code: undefined) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成描边颜色\n.wuji-border-color(@code: undefined, @position: around) {\n    @color-default: getColor(@code);\n    @prop: if(@position = around, e('border-color'), e('border-@{position}-color'));\n    @{prop}: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                @{prop}: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                @{prop}: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            @{prop}: @color-default;\n        }\n    }\n}\n\n// mixin: 生成描边颜色\n// 当第一个参数是多值情况且固定顺序 <line-width> <line-style> <color> 时\n// 会调用line/mixin.less中的wuji-border方法，参数示例：1px solid red\n.wuji-border(@code: undefined, @position: around) when (length(@code) = 1) {\n    .wuji-border-color(@code, @position);\n}\n\n// mixn: 生成上描边颜色\n.wuji-border-top-color(@code: undefined) {\n    .wuji-border-color(@code, top);\n}\n\n// mixn: 生成下描边颜色\n.wuji-border-bottom-color(@code: undefined) {\n    .wuji-border-color(@code, bottom);\n}\n\n// mixn: 生成左描边颜色\n.wuji-border-left-color(@code: undefined) {\n    .wuji-border-color(@code, left);\n}\n\n// mixn: 生成右描边颜色\n.wuji-border-right-color(@code: undefined) {\n    .wuji-border-color(@code, right);\n}\n\n// mixin: 生成文字颜色\n.wuji-color(@code: undefined) {\n    @color-default: getColor(@code);\n    color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成svg填充颜色\n.wuji-fill(@code: undefined, @night-mode: true) {\n    @color-default: getColor(@code);\n    fill: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                fill: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                fill: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            fill: @color-default;\n        }\n    }\n}\n\n// mixin: 生成光标颜色\n.wuji-caret(@code: undefined) {\n    @color-default: getColor(@code);\n    caret-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                caret-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                caret-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            caret-color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成透明度\n.wuji-opacity(@code: undefined) {\n    opacity: getOpacity(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.wuji-root(\n    @color: CAM_X791, // GC1\n    @background: CAM_X392, // GC9\n    @selector-root: @selector-root,\n    @selector-dark: @selector-dark,\n    @selector-night: @selector-night\n) {\n    // 如果选择器参数是字符串类型，需要escape\n    @rule1: if(isstring(@selector-root), e(@selector-root), @selector-root);\n    @rule2: if(isstring(@selector-dark), e(@selector-dark), @selector-dark);\n    @rule3: if(isstring(@selector-night), e(@selector-night), @selector-night);\n\n    @{rule1} {\n        color: getColor(@color);\n        background-color: getColor(@background);\n\n        & when (@multi-mode = true) and (@dark-mode = true) {\n            &@{rule2} {\n                color: getColor(@color, Dark);\n                background-color: getColor(@background, Dark);\n            }\n        }\n\n        & when (@multi-mode = true) and (@night-mode = true) {\n            &@{rule3} {\n                color: getColor(@color, Night);\n                background-color: getColor(@background, Night);\n            }\n        }\n    }\n}\n","/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n\n// 背景图\n.coverimg() {\n    background-position: 50% 50%;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n// 全局字号调整\n.font-size-change() {\n    .font-scale(@font-size, @line-height, @font-scale: 1) {\n        font-size: @font-size / @font-base * @font-scale;\n        line-height: @line-height / @font-base * @font-scale;\n    }\n\n    .font-size-0 & {\n        .font-scale(42px, 66px);\n    }\n    .font-size-1 & {\n        .font-scale(51px, 78px);\n    }\n    .font-size-2 & {\n        .font-scale(57px, 78px);\n    }\n    .font-size-3 & {\n        .font-scale(63px, 84px);\n    }\n\n    @media screen and (max-width: 360px) {\n        .font-size-0 & {\n            .font-scale(42px, 66px, 0.9);\n        }\n        .font-size-1 & {\n            .font-scale(51px, 78px, 0.9);\n        }\n        .font-size-2 & {\n            .font-scale(57px, 78px, 0.9);\n        }\n        .font-size-3 & {\n            .font-scale(63px, 84px, 0.9);\n        }\n    }\n}\n\n// 夜间模式替换背景图\n.nightmode(@icon, @flag: true, @join: false) when (@flag = false) {\n    background-image: url('@{icon-path}/@{icon}');\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = false) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    @{selector-night} & {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = true) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    .nightmode& {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n\n// 熊掌占位图\n.placeholder() {\n    .theme-image('bd-logo.png');\n}\n\n// mixin: 复合属性radius不同media下的运算\n.calc-radius(@radius: 0, @ratio: 1) {\n    each(@radius, {\n        border-radius+_: extract(@radius, @key) * @ratio;\n    });\n}\n\n// 1px 边框说明: position有效值为 [top, right, bottom, left, around]; position为around 时圆角 radius 有效显示四条边\n.hairline(@position: bottom, @color: #000, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border: 1px solid @color;\n    border-radius: @radius;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.hairline(@position: bottom, @color: #000) when not (@position = around) {\n    position: relative;\n    border: 1px solid transparent;\n    border-@{position}-color: @color;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n}\n\n// 绝对定位或固定定位填充满父元素或祖父元素\n.fullfill(@position: absolute) {\n    position: @position;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n}\n\n// 容器内容居中\n.centerfy(@direction: row) {\n    display: flex;\n    flex-direction: @direction;\n    align-items: center;\n    justify-content: center;\n}\n\n// 多行截断\n.line-clamp(@line: 2) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: @line;\n    -webkit-box-orient: vertical;\n    word-break: break-all;\n}\n\n// 单行截断\n.ellipsis() {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 0;\n}\n\n// 清除浮动\n.clearfix() {\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n// 生成纯色填充的svg背景图\n.svg-background(@color: #fff) {\n    @svg: escape('<svg xmlns=\"http://www.w3.org/2000/svg\"><rect fill=\"@{color}\" width=\"100%\" height=\"100%\"/></svg>');\n    background-image: url('data:image/svg+xml;charset=utf8,@{svg}');\n}\n"]}]);
