/**
 * @file store 文件
 */

import {builder} from 'san-update';
import {store} from '@/lib/Store';

store.initData({
    pageInit: false,
    // 分享配置信息
    shareInfo: '',
}).addActions({
    setPageInit(data) {
        return builder().set('pageInit', data);
    },
    // 设置分享数据
    setShareInfo(data) {
        return builder().set('shareInfo', data);
    },
});
