    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 200%;\n    height: 200%;\n    -webkit-transform: scale(0.5);\n            transform: scale(0.5);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n  .s-image-hairline:after {\n    border-radius: 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .s-image-hairline {\n    border: none;\n  }\n  .s-image-hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 300%;\n    height: 300%;\n    -webkit-transform: scale(0.33333333);\n            transform: scale(0.33333333);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n  .s-image-hairline:after {\n    border-radius: 0;\n  }\n}\n.s-image-default {\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  background-image: url(" + ___CSS_LOADER_URL___0___ + ");\n  background-color: rgba(245, 246, 249, 1);\n  background-position: 50% 50%;\n  background-repeat: no-repeat;\n  background-size: 0.29rem 0.29rem;\n}\n.darkmode .s-image-default {\n  background-image: url(" + ___CSS_LOADER_URL___1___ + ");\n}\n.nightmode .s-image-default {\n  background-image: url(" + ___CSS_LOADER_URL___2___ + ");\n}\n.darkmode .s-image-default {\n  background-color: rgba(44, 44, 44, 1);\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/style/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/image/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/theme/_theme.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less"],"names":[],"