lay: flex;\n  flex-wrap: nowrap;\n}\n.s-row-flex .w-100 {\n  width: 100%!important;\n}\n.s-row-wrap {\n  flex-wrap: wrap;\n}\n.s-row-justify-center {\n  justify-content: center;\n}\n.s-row-justify-end {\n  justify-content: flex-end;\n}\n.s-row-justify-space-between {\n  justify-content: space-between;\n}\n.s-row-justify-space-around {\n  justify-content: space-around;\n}\n.s-row-align-center {\n  align-items: center;\n}\n.s-row-align-bottom {\n  align-items: flex-end;\n}\n.s-col-1 {\n  flex: 0 0 8.33333333%;\n  max-width: 8.33333333%;\n  width: 8.33333333%;\n}\n.s-col-2 {\n  flex: 0 0 16.66666667%;\n  max-width: 16.66666667%;\n  width: 16.66666667%;\n}\n.s-col-3 {\n  flex: 0 0 25%;\n  max-width: 25%;\n  width: 25%;\n}\n.s-col-4 {\n  flex: 0 0 33.33333333%;\n  max-width: 33.33333333%;\n  width: 33.33333333%;\n}\n.s-col-5 {\n  flex: 0 0 41.66666667%;\n  max-width: 41.66666667%;\n  width: 41.66666667%;\n}\n.s-col-6 {\n  flex: 0 0 50%;\n  max-width: 50%;\n  width: 50%;\n}\n.s-col-7 {\n  flex: 0 0 58.33333333%;\n  max-width: 58.33333333%;\n  width: 58.33333333%;\n}\n.s-col-8 {\n  flex: 0 0 66.66666667%;\n  max-width: 66.66666667%;\n  width: 66.66666667%;\n}\n.s-col-9 {\n  flex: 0 0 75%;\n  max-width: 75%;\n  width: 75%;\n}\n.s-col-10 {\n  flex: 0 0 83.33333333%;\n  max-width: 83.33333333%;\n  width: 83.33333333%;\n}\n.s-col-11 {\n  flex: 0 0 91.66666667%;\n  max-width: 91.66666667%;\n  width: 91.66666667%;\n}\n.s-col-12 {\n  flex: 0 0 100%;\n  max-width: 100%;\n  width: 100%;\n}\n.s-col-offset-1 {\n  margin-left: 8.33333333%;\n}\n.s-col-offset-2 {\n  margin-left: 16.66666667%;\n}\n.s-col-offset-3 {\n  margin-left: 25%;\n}\n.s-col-offset-4 {\n  margin-left: 33.33333333%;\n}\n.s-col-offset-5 {\n  margin-left: 41.66666667%;\n}\n.s-col-offset-6 {\n  margin-left: 50%;\n}\n.s-col-offset-7 {\n  margin-left: 58.33333333%;\n}\n.s-col-offset-8 {\n  margin-left: 66.66666667%;\n}\n.s-col-offset-9 {\n  margin-left: 75%;\n}\n.s-col-offset-10 {\n  margin-left: 83.33333333%;\n}\n.s-col-offset-11 {\n  margin-left: 91.66666667%;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 网格布局\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-col: s-col;\n@prefix-row: s-row;\n@columns: 12;\n@lr-space: @M;\n\n.make-col(@size, @columns: @columns) {\n    flex: 0 0 percentage(@size / @columns);\n    max-width: percentage(@size / @columns);\n    width: percentage(@size / @columns);\n}\n\n.make-col-offset(@size, @columns: @columns) {\n    margin-left: percentage(@size / @columns);\n}\n\n.@{prefix-row} {\n    margin-left: @lr-space;\n    margin-right: @lr-space;\n    max-width: 100%;\n    // overflow: hidden;\n\n    &-float {\n        .clearfix();\n\n        .@{prefix-col} {\n            float: left;\n        }\n    }\n\n    &-flex {\n        display: flex;\n        flex-wrap: nowrap;\n\n        // .@{prefix-col} {\n        //     flex-basis: 0;\n        //     flex-grow: 1;\n        //     max-width: 100%;\n        //     word-break: break-all;\n        //     overflow: hidden;\n        // }\n\n        .w-100 {\n            width: 100%!important;\n        }\n    }\n\n    &-wrap {\n        flex-wrap: wrap;\n    }\n\n    &-justify-center {\n        justify-content: center;\n    }\n\n    &-justify-end {\n        justify-content: flex-end;\n    }\n\n    &-justify-space-between {\n        justify-content: space-between;\n    }\n\n    &-justify-space-around {\n        justify-content: space-around;\n    }\n\n    &-align-center {\n        align-items: center;\n    }\n\n    &-align-bottom {\n        align-items: flex-end;\n    }\n}\n\n// 生成 s-col-# 和 s-col-offset-#\neach(range(@columns), {\n    .@{prefix-col}-@{value} {\n        .make-col(@value);\n    }\n});\neach(range(@columns - 1), {\n    .@{prefix-col}-offset-@{value} {\n        .make-col-offset(@value);\n    }\n});\n","/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n\n// 背景图\n.coverimg() {\n    background-position: 50% 50%;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n// 全局字号调整\n.font-size-change() {\n    .font-scale(@font-size, @line-height, @font-scale: 1) {\n        font-size: @font-size / @font-base * @font-scale;\n        line-height: @line-height / @font-base * @font-scale;\n    }\n\n    .font-size-0 & {\n        .font-scale(42px, 66px);\n    }\n    .font-size-1 & {\n        .font-scale(51px, 78px);\n    }\n    .font-size-2 & {\n        .font-scale(57px, 78px);\n    }\n    .font-size-3 & {\n        .font-scale(63px, 84px);\n    }\n\n    @media screen and (max-width: 360px) {\n        .font-size-0 & {\n            .font-scale(42px, 66px, 0.9);\n        }\n        .font-size-1 & {\n            .font-scale(51px, 78px, 0.9);\n        }\n        .font-size-2 & {\n            .font-scale(57px, 78px, 0.9);\n        }\n        .font-size-3 & {\n            .font-scale(63px, 84px, 0.9);\n        }\n    }\n}\n\n// 夜间模式替换背景图\n.nightmode(@icon, @flag: true, @join: false) when (@flag = false) {\n    background-image: url('@{icon-path}/@{icon}');\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = false) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    @{selector-night} & {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = true) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    .nightmode& {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n\n// 熊掌占位图\n.placeholder() {\n    .theme-image('bd-logo.png');\n}\n\n// mixin: 复合属性radius不同media下的运算\n.calc-radius(@radius: 0, @ratio: 1) {\n    each(@radius, {\n        border-radius+_: extract(@radius, @key) * @ratio;\n    });\n}\n\n// 1px 边框说明: position有效值为 [top, right, bottom, left, around]; position为around 时圆角 radius 有效显示四条边\n.hairline(@position: bottom, @color: #000, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border: 1px solid @color;\n    border-radius: @radius;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.hairline(@position: bottom, @color: #000) when not (@position = around) {\n    position: relative;\n    border: 1px solid transparent;\n    border-@{position}-color: @color;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n}\n\n// 绝对定位或固定定位填充满父元素或祖父元素\n.fullfill(@position: absolute) {\n    position: @position;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n}\n\n// 容器内容居中\n.centerfy(@direction: row) {\n    display: flex;\n    flex-direction: @direction;\n    align-items: center;\n    justify-content: center;\n}\n\n// 多行截断\n.line-clamp(@line: 2) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: @line;\n    -webkit-box-orient: vertical;\n    word-break: break-all;\n}\n\n// 单行截断\n.ellipsis() {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 0;\n}\n\n// 清除浮动\n.clearfix() {\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n// 生成纯色填充的svg背景图\n.svg-background(@color: #fff) {\n    @svg: escape('<svg xmlns=\"http://www.w3.org/2000/svg\"><rect fill=\"@{color}\" width=\"100%\" height=\"100%\"/></svg>');\n    background-image: url('data:image/svg+xml;charset=utf8,@{svg}');\n}\n"]}]);
