/**
 * @file 榜单列表组件
 */

import {Component} from 'san';
import './index.less';
import {invokeScheme} from '@/lib/utils/common';
import {sendShowLog, sendClickLog} from '../../log';

export default class App extends Component {
    static template = /* html */ `
        <div class="daylistWrap">
            <div class="containerWrap">
                <div class="themeWrap">
                    <div class="list" s-for="item, index in awarded.list">
                        <div class="listWrap" on-click="ranklistClick(item)">
                            <div class=
                                "rankIcon
                                rankIcon_{{index}}
                                {{index < 3 ? 'rankIconW' : 'rankIconR'}}"
                            >{{index < 3 ? '' : (index < 9 ? '0' + (index + 1) : (index + 1))}}</div>
                            <div class="videoCover" style="background: url({{item.image}}) no-repeat center/cover;"></div>
                            <div class="authorInfo">
                                <div class="author">
                                    <img s-if="item.avatar" src="{{item.avatar}}"/>
                                    <span>{{item.source}}</span>
                                </div>
                                <span class="videoTitle">{{item.title}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    `;

    static computed = {
        awarded() {
            return this.data.get('resData')?.awarded_users || {};
        }
    };

    ranklistClick(item) {
        sendClickLog({
            value: 'weekly_board_video',
            ext: {
                nid: item.nid,
            }
        });
        if (item?.cmd) {
            invokeScheme(item.cmd);
        }        
    }

    initData() {
        return {

        };
    };

    attached() {
    };
}
