exports.push([module.id, "/**\n * @file 组件样式 环形进度条\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-circle {\n  position: relative;\n  overflow: hidden;\n  text-align: center;\n}\n.s-circle svg {\n  width: 100%;\n  height: 100%;\n}\n.s-circle-content {\n  display: -webkit-flex;\n  display: flex;\n  -webkit-flex-direction: row;\n          flex-direction: row;\n  -webkit-align-items: center;\n          align-items: center;\n  -webkit-justify-content: center;\n          justify-content: center;\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n}\n.s-circle-text {\n  font-size: 0.12rem;\n  line-height: 1;\n  letter-spacing: 0;\n  color: #fff;\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/circle/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/style/mixins.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECUG,kBAAA;EACA,gBAAA;EACA,kBAAA;AFmBJ;AC/BC;ECeO,WAAA;EACA,YAAA;AFmBR;AEhBI;ECoJA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;EDrJI,kBAAA;EACA,OAAA;EACA,MAAA;EACA,WAAA;EACA,YAAA;AFqBR;AElBI;EACI,kBAAA;EACA,cAAA;EACA,iBAAA;EACA,WAAA;AFoBR","file":"index.less","sourcesContent":["/**\n * @file 组件样式 环形进度条\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-circle {\n  position: relative;\n  overflow: hidden;\n  text-align: center;\n}\n.s-circle svg {\n  width: 100%;\n  height: 100%;\n}\n.s-circle-content {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  position: absolute;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n}\n.s-circle-text {\n  font-size: 36pr;\n  line-height: 1;\n  letter-spacing: 0;\n  color: #fff;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 环形进度条\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-cls: s-circle;\n\n.@{prefix-cls} {\n    position: relative;\n    overflow: hidden;\n    text-align: center;\n\n    svg {\n        width: 100%;\n        height: 100%;\n    }\n\n    &-content {\n        .centerfy();\n        position: absolute;\n        left: 0;\n        top: 0;\n        width: 100%;\n        height: 100%;\n    }\n\n    &-text {\n        font-size: 36pr;\n        line-height: 1;\n        letter-spacing: 0;\n        color: #fff;\n    }\n}\n","/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n\n// 背景图\n.coverimg() {\n    background-position: 50% 50%;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n// 全局字号调整\n.font-size-change() {\n    .font-scale(@font-size, @line-height, @font-scale: 1) {\n        font-size: @font-size / @font-base * @font-scale;\n        line-height: @line-height / @font-base * @font-scale;\n    }\n\n    .font-size-0 & {\n        .font-scale(42px, 66px);\n    }\n    .font-size-1 & {\n        .font-scale(51px, 78px);\n    }\n    .font-size-2 & {\n        .font-scale(57px, 78px);\n    }\n    .font-size-3 & {\n        .font-scale(63px, 84px);\n    }\n\n    @media screen and (max-width: 360px) {\n        .font-size-0 & {\n            .font-scale(42px, 66px, 0.9);\n        }\n        .font-size-1 & {\n            .font-scale(51px, 78px, 0.9);\n        }\n        .font-size-2 & {\n            .font-scale(57px, 78px, 0.9);\n        }\n        .font-size-3 & {\n            .font-scale(63px, 84px, 0.9);\n        }\n    }\n}\n\n// 夜间模式替换背景图\n.nightmode(@icon, @flag: true, @join: false) when (@flag = false) {\n    background-image: url('@{icon-path}/@{icon}');\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = false) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    @{selector-night} & {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = true) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    .nightmode& {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n\n// 熊掌占位图\n.placeholder() {\n    .theme-image('bd-logo.png');\n}\n\n// mixin: 复合属性radius不同media下的运算\n.calc-radius(@radius: 0, @ratio: 1) {\n    each(@radius, {\n        border-radius+_: extract(@radius, @key) * @ratio;\n    });\n}\n\n// 1px 边框说明: position有效值为 [top, right, bottom, left, around]; position为around 时圆角 radius 有效显示四条边\n.hairline(@position: bottom, @color: #000, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border: 1px solid @color;\n    border-radius: @radius;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.hairline(@position: bottom, @color: #000) when not (@position = around) {\n    position: relative;\n    border: 1px solid transparent;\n    border-@{position}-color: @color;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n}\n\n// 绝对定位或固定定位填充满父元素或祖父元素\n.fullfill(@position: absolute) {\n    position: @position;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n}\n\n// 容器内容居中\n.centerfy(@direction: row) {\n    display: flex;\n    flex-direction: @direction;\n    align-items: center;\n    justify-content: center;\n}\n\n// 多行截断\n.line-clamp(@line: 2) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: @line;\n    -webkit-box-orient: vertical;\n    word-break: break-all;\n}\n\n// 单行截断\n.ellipsis() {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 0;\n}\n\n// 清除浮动\n.clearfix() {\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n// 生成纯色填充的svg背景图\n.svg-background(@color: #fff) {\n    @svg: escape('<svg xmlns=\"http://www.w3.org/2000/svg\"><rect fill=\"@{color}\" width=\"100%\" height=\"100%\"/></svg>');\n    background-image: url('data:image/svg+xml;charset=utf8,@{svg}');\n}\n"]}]);
