 absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-position: 50% 50%;\n  background-size: 100% 100%;\n}\n.darkmode .s-flash-mask {\n  background-image: url(\"data:image/png;base64,\");\n}\n.nightmode .s-flash-mask {\n  background-image: url(\"data:image/png;base64,\");\n}\n@-webkit-keyframes flash {\n  0% {\n    -webkit-transform: translateX(0);\n            transform: translateX(0);\n  }\n  100% {\n    -webkit-transform: translateX(100%);\n            transform: translateX(100%);\n  }\n}\n@keyframes flash {\n  0% {\n    -webkit-transform: translateX(0);\n            transform: translateX(0);\n  }\n  100% {\n    -webkit-transform: translateX(100%);\n            transform: translateX(100%);\n  }\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/style/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/flash/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/theme/_theme.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECuKG,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;EC/JA,eAAA;UAAA,OAAA;EACA,gBAAA;AHsBJ;AGnBI;ED+IA,eAAA;EACA,OAAA;EACA,MAAA;EACA,WAAA;EACA,YAAA;EAKA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;AF7HJ;AGzBI;EACI,kBAAA;EACA,gBAAA;EACA,cAAA;EACA,cAAA;EACA,kBAAA;EACA,4BAAA;EACA,0BAAA;ED2BJ,+CAAA;AFCJ;ACvDC;ECyDO,+CAAA;AFCR;AC1DC;EE+BW,sBAAA;EACA,0CAAA;AH8BZ;AG1BI;EACI,YAAA;EACA,WAAA;EACA,sCAAA;UAAA,8BAAA;EACA,4BAAA;EACA,0BAAA;EC0HA,+CAAA;AJ7FR;ACtEC;EG0KW,+CAAA;AJjGZ;ACzEC;EGkLW,+CAAA;AJtGZ;AG/BI;EACI,kBAAA;EACA,MAAA;EACA,QAAA;EACA,SAAA;EACA,OAAA;EACA,4BAAA;EACA,0BAAA;AHiCR;ACrFC;EG0KW,mwJAAA;AJlFZ;ACxFC;EGkLW,2tJAAA;AJvFZ;AGlCA;EACI;IACI,gCAAA;YAAA,wBAAA;EHoCN;EGlCE;IACI,mCAAA;YAAA,2BAAA;EHoCN;AACF;AG1CA;EACI;IACI,gCAAA;YAAA,wBAAA;EHoCN;EGlCE;IACI,mCAAA;YAAA,2BAAA;EHoCN;AACF","file":"index.less","sourcesContent":["/**\n * @file 组件样式 熊掌扫光动效\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-flash {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  flex: 1;\n  min-height: 300pr;\n}\n.s-flash-fullpage {\n  position: fixed;\n  left: 0;\n  top: 0;\n  width: 100%;\n  height: 100%;\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n}\n.s-flash-logo {\n  position: relative;\n  overflow: hidden;\n  margin: 0 auto;\n  width: 441pr;\n  height: 142pr;\n  background-repeat: no-repeat;\n  background-size: 100% 100%;\n  background-image: url('../../assets/img/flash-logo.png');\n}\n.nightmode .s-flash-logo {\n  background-image: url('../../assets/img/nightmode-flash-logo.png');\n}\n.darkmode .s-flash-logo {\n  background-image: none;\n  background-color: rgba(255, 255, 255, 0.1);\n}\n.s-flash-flash {\n  height: 100%;\n  width: 100%;\n  animation: flash 1.3s infinite;\n  background-repeat: no-repeat;\n  background-size: auto 100%;\n  background-image: url('../../assets/img/flash-light.png');\n}\n.darkmode .s-flash-flash {\n  background-image: url('../../assets/img/darkmode-flash-light.png');\n}\n.nightmode .s-flash-flash {\n  background-image: url('../../assets/img/nightmode-flash-light.png');\n}\n.s-flash-mask {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  left: 0;\n  background-position: 50% 50%;\n  background-size: 100% 100%;\n}\n.darkmode .s-flash-mask {\n  background-image: url(\"data:image/png;base64,\");\n}\n.nightmode .s-flash-mask {\n  background-image: url(\"data:image/png;base64,\");\n}\n@keyframes flash {\n  0% {\n    transform: translateX(0);\n  }\n  100% {\n    transform: translateX(100%);\n  }\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n\n// 背景图\n.coverimg() {\n    background-position: 50% 50%;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n// 全局字号调整\n.font-size-change() {\n    .font-scale(@font-size, @line-height, @font-scale: 1) {\n        font-size: @font-size / @font-base * @font-scale;\n        line-height: @line-height / @font-base * @font-scale;\n    }\n\n    .font-size-0 & {\n        .font-scale(42px, 66px);\n    }\n    .font-size-1 & {\n        .font-scale(51px, 78px);\n    }\n    .font-size-2 & {\n        .font-scale(57px, 78px);\n    }\n    .font-size-3 & {\n        .font-scale(63px, 84px);\n    }\n\n    @media screen and (max-width: 360px) {\n        .font-size-0 & {\n            .font-scale(42px, 66px, 0.9);\n        }\n        .font-size-1 & {\n            .font-scale(51px, 78px, 0.9);\n        }\n        .font-size-2 & {\n            .font-scale(57px, 78px, 0.9);\n        }\n        .font-size-3 & {\n            .font-scale(63px, 84px, 0.9);\n        }\n    }\n}\n\n// 夜间模式替换背景图\n.nightmode(@icon, @flag: true, @join: false) when (@flag = false) {\n    background-image: url('@{icon-path}/@{icon}');\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = false) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    @{selector-night} & {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = true) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    .nightmode& {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n\n// 熊掌占位图\n.placeholder() {\n    .theme-image('bd-logo.png');\n}\n\n// mixin: 复合属性radius不同media下的运算\n.calc-radius(@radius: 0, @ratio: 1) {\n    each(@radius, {\n        border-radius+_: extract(@radius, @key) * @ratio;\n    });\n}\n\n// 1px 边框说明: position有效值为 [top, right, bottom, left, around]; position为around 时圆角 radius 有效显示四条边\n.hairline(@position: bottom, @color: #000, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border: 1px solid @color;\n    border-radius: @radius;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.hairline(@position: bottom, @color: #000) when not (@position = around) {\n    position: relative;\n    border: 1px solid transparent;\n    border-@{position}-color: @color;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n}\n\n// 绝对定位或固定定位填充满父元素或祖父元素\n.fullfill(@position: absolute) {\n    position: @position;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n}\n\n// 容器内容居中\n.centerfy(@direction: row) {\n    display: flex;\n    flex-direction: @direction;\n    align-items: center;\n    justify-content: center;\n}\n\n// 多行截断\n.line-clamp(@line: 2) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: @line;\n    -webkit-box-orient: vertical;\n    word-break: break-all;\n}\n\n// 单行截断\n.ellipsis() {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 0;\n}\n\n// 清除浮动\n.clearfix() {\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n// 生成纯色填充的svg背景图\n.svg-background(@color: #fff) {\n    @svg: escape('<svg xmlns=\"http://www.w3.org/2000/svg\"><rect fill=\"@{color}\" width=\"100%\" height=\"100%\"/></svg>');\n    background-image: url('data:image/svg+xml;charset=utf8,@{svg}');\n}\n","/**\n * @file 组件样式 熊掌扫光动效\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@prefix-cls: s-flash;\n\n.@{prefix-cls} {\n    .centerfy();\n    flex: 1;\n    min-height: 300pr;\n\n    // 全屏fixed垂直居中\n    &-fullpage {\n        .fullfill(fixed);\n        .centerfy();\n    }\n\n    &-logo {\n        position: relative;\n        overflow: hidden;\n        margin: 0 auto;\n        width: 441pr;\n        height: 142pr;\n        background-repeat: no-repeat;\n        background-size: 100% 100%;\n        .nightmode('flash-logo.png');\n\n        @{selector-dark} & {\n            background-image: none;\n            background-color: rgba(255,255,255,0.1);\n        }\n    }\n\n    &-flash {\n        height: 100%;\n        width: 100%;\n        animation: flash 1.3s infinite;\n        background-repeat: no-repeat;\n        background-size: auto 100%;\n        .theme-image('flash-light.png');\n    }\n\n    &-mask {\n        position: absolute;\n        top: 0;\n        right: 0;\n        bottom: 0;\n        left: 0;\n        background-position: 50% 50%;\n        background-size: 100% 100%;\n        .theme-image('flash-mask.png', false, true, true, true);\n    }\n}\n\n@keyframes flash {\n    0% {\n        transform: translateX(0);\n    }\n    100% {\n        transform: translateX(100%);\n    }\n}\n","/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n\n// 是否使用原生CSS变量，默认false\n@use-cssvar: false;\n// 是否支持多种模式：默认模式，暗黑模式，夜间模式；false 时只支持默认模式\n@multi-mode: true;\n// 是否支持暗黑模式, @multi-mode 为 true 时有效\n@dark-mode: true;\n// 是否支持夜间模式, @multi-mode 为 true 时有效\n@night-mode: false;\n// 主题颜色全局选择器，定义在根元素上，默认根元素html，可通过赋值改变\n@selector-root: html;\n// 日间模式 类选择器 可用于暗黑模式下部分区域依旧需要保持日间模式的情况\n@selector-default: '';\n// 深色模式 类选择器\n@selector-dark: .darkmode;\n// 夜间模式 类选择器\n@selector-night: .nightmode;\n\n// 主题图标路径\n@image-path: '../assets/img';\n\n.use-cssvar() when(@use-cssvar = true) {\n}\n\n// mixin: 生成文字颜色\n.theme-color(@code) when (@use-cssvar = true) {\n}\n.theme-color(@code) when (@use-cssvar = false) {\n    .wuji-color(@code);\n}\n\n// mixin: 生成svg填充颜色\n.theme-fill(@code) when (@use-cssvar = true) {\n}\n.theme-fill(@code) when (@use-cssvar = false) {\n    .wuji-fill(@code);\n}\n\n// mixin: 生成背景颜色\n.theme-background(@code) when (@use-cssvar = true) {\n}\n.theme-background(@code) when (@use-cssvar = false) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成边框颜色\n.theme-border(@code) when (@use-cssvar = true) {\n}\n.theme-border(@code) when (@use-cssvar = false) {\n    .wuji-border-color(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.theme-root() {\n    .wuji-root();\n}\n\n// hack: 安卓夜间模式1px看不清，通过在伪元素:after上定义rgba颜色后再微调透明度\n.hairline-nightmode() {\n    @{selector-night} &:after {\n        border-color: rgba(0,0,0,0.9);\n        background-color: rgba(0,0,0,0.01);\n        opacity: .3;\n    }\n}\n\n// 根据media query缩放，dpr: 1.5、2、3\n.hairline-media(@dpr, @position) {\n    border: none;\n\n    &:after {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        pointer-events: none;\n        width: @dpr * 100%;\n        height: @dpr * 100%;\n        transform: scale(1 / @dpr);\n        transform-origin: 0 0;\n    }\n}\n\n// mixin: 使用色表编号的1px边框，不支持自定义颜色，自定义颜色可使用.hairline(...)\n.theme-hairline(@position, @code: GC34, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border-radius: @radius;\n    .theme-border(@code);\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.theme-hairline(@position: bottom, @code: GC34) when not (@position = around) {\n    position: relative;\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border-@{position}: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n    }\n}\n\n// mixin: 不同主题下背景图路径\n.theme-image(@image, @light: true, @night: true, @dark: true, @base64: false) {\n    @output4light: if(@light = true, {\n        @img: '@{image-path}/@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        background-image: @url;\n    }, {});\n\n    @output4dark: if(@dark = true, {\n        @img: '@{image-path}/darkmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-dark} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4night: if(@night = true, {\n        @img: '@{image-path}/nightmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-night} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4light();\n\n    @output4dark();\n\n    @output4night();\n}\n"]}]);
