/**
 * @file 高考征文
 */
import './app.less';
import {Component} from 'san';
import {connect} from '@/lib/Store';
import {
    Flash,
    Toast as toast,
} from '@baidu/nano';
import fly from 'flyio';
import boxx from '@baidu/boxx';
import Zeno from '../components/zeno';
import RankList from '../components/ranklistNew';
import {pageURL} from '../const';

import {getData} from '../service';
import {invokeScheme} from '@/lib/utils/common';
import {sendShowLog, sendClickLog} from '../log';
import {env} from '@baidu/auc-base/env';
import versionCompare from '@baidu/xbox/version_compare';
import {getProcessInfo, datachannelRegister, datachannelUnRegister, watchPageState} from '../common';
const loginCbName = `loginCb${Math.floor(Math.random() * 10e3)}`;

export default class App extends Component {
    constructor(opt) {
        super(opt);
    }
    static template = /* html */ `
        <div>
            <nano-flash s-show="!pageInit" layout="full"/>
            <!-- status 0 活动中 -->
            <div s-show="isShowHome" class="main">
                <div class="headContainer">
                    <!-- 头图 -->
                    <div class="headWrap">
                        <div s-show="isSucPublish" class="popPublish"></div>
                        <div class="headVideo" on-click="jumpAward"></div>
                        <div class="headRule squareW" on-click="jumpRule"></div>
                        <div class="headShare squareW" on-click="jumpShare"></div>
                    </div>
                    <!-- 案例专区1,2,3 -->
                    <zeno-card
                        on-pubBtnClick="handlePubBtnClick"
                        data="{{resData.multi_candidate_zone}}"
                        firstFingerClick="{{firstFingerClick}}"
                    ></zeno-card>
                </div>
                <!-- 参与方式 -->
                <div class="partMethod" id="partMethod"></div>
                <!-- 活动奖励 -->
                <div class="activityReward" id="activityReward"></div>
                <!-- 优质排行 -->
                <div class="listCard" id="listCard"></div>
                <list-card
                    s-if="{{resData.awarded_users.list.length > 0}}"
                    resData="{{resData}}"
                ></list-card>
                <div class="line"></div>
            </div>
        </div>
    `;
    static components = {
        'nano-flash': Flash,
        'zeno-card': Zeno,
        'list-card': RankList,
    };
    static computed = {};

    // 初始化分享数据
    initShareData() {
        window.BoxShareData = {
            options: {
                type: 'url',
                mediaType: 'all',
                linkUrl: pageURL.SHARE,
                title: '热点挑战赛',
                content: '来百度拍视频赢大奖！最高可得1000元！',
                iconUrl: 'https://b.bdstatic.com/searchbox/image/gcp/20230825/4184765539.jpg',
                imageUrl: 'https://b.bdstatic.com/searchbox/image/gcp/20230825/4184765539.jpg',
            },
        };
    }

    toLogin(isReload) {
        // NA登陆面板的回调
        window[loginCbName] = payload => {
            try {
                payload = JSON.parse(payload);
            }
            catch (error) {
                // eslint-disable-next-line no-console
                console.log('login nextCallback payload pase error', error);
            }

            // 登陆成功
            if (payload && !+payload?.status && payload?.data?.status === '1') {
                this.data.set('isLogin', 1);

                if (isReload) {
                    window.location.reload();
                };
            }
        };

        const scheme = `baiduboxapp://v26/account/login?params=%7B%22loginType%22%3A%22sms%22%2C%22showThirdLogin%22%3A1%2C%22loginSource%22%3A%22%22%2C%22normalizeAccount%22%3A0%2C%22loginCallback%22%3A%22${loginCbName}%22%7D&callback=console.log&upgrade=0`;
        invokeScheme(scheme);
    }

    // method：调起个人视频
    // jumpVideo() {
    //     sendClickLog({value: 'my_publish'});
    //     const {my_video: myVideo} = this.data.get('resData') || {};
    //     const isLogin = this.data.get('isLogin');
    //     // 兜底：跳个人中心先登陆
    //     if (!isLogin) {
    //         this.toLogin(true);
    //         return;
    //     };

    //     if (myVideo?.cmd) {
    //         invokeScheme(myVideo.cmd);
    //     }
    // }

    // method：调起奖品页（我的视频）
    jumpAward() {
        sendClickLog({value: 'my_publish'});
        const {my_assets: myAssets} = this.data.get('resData') || {};
        const isLogin = this.data.get('isLogin');
        // 兜底：先登陆在看钱包
        if (!isLogin) {
            this.toLogin(true);
            return;
        };

        if (myAssets?.cmd) {
            invokeScheme(myAssets.cmd);
        }
    }

    // method：调起规则页
    jumpRule() {
        sendClickLog({value: 'rule_entry'});
        window.location.href = pageURL.RULE;
    }

    // method：调起分享面板
    jumpShare() {
        sendClickLog({value: 'share_entry'});
        try {
            window.successcallback = res => {
                const resp = typeof(res) === 'string' ? JSON.parse(res) : res;
                if (resp?.data?.status === '0') {
                    boxx.call('commonUI.showToast', {
                        type: '1',
                        message: '分享成功'
                    });
                    return;
                }
            };
            const params = {
                shareData: window.BoxShareData.options,
                shareCallback: 'successcallback',
            };
            invokeScheme(`baiduboxapp://share/doShare?params=${encodeURIComponent(JSON.stringify(params))}`);
        } catch (err) {
            console.log(err);
        }
    }

    // handle：几个发布点击
    handlePubBtnClick(args) {
        const firstFingerClick = this.data.get('firstFingerClick');
        firstFingerClick && this.data.set('firstFingerClick', false);
        const {cmd = ''} = args || {};
        // const isFinishInfo = this.data.get('isFinishInfo');
        const isLogin = this.data.get('isLogin');

        // 先登陆
        if (!isLogin) {
            this.toLogin(true);
            return;
        };
        invokeScheme(cmd);
    }

    // 发布器状态
    profileugcstatus() {
        getProcessInfo();
        datachannelRegister();
    }

    // 活动状态
    setActivity() {
        const {activity_status: activityStatus} = this.data.get('resData') || {};
        if (+activityStatus === 2) {
            // 已经结束
            window.location.href = pageURL.EDN;
        } else if (+activityStatus === 3) {
            // 已经下线
            window.location.href = pageURL.OFFLINE;
        }
    }

    // 取消发布器toast
    setPubToast() {
        if (!env.isBoxSeries) {
            return;
        };
        const params = {
            isShow: 1,
        };
        // 取消toast https://ku.baidu-int.com/knowledge/HFVrC7hq1Q/H0jzXv88Jb/dyogYYVcTY/oauTm-ICk744kW
        invokeScheme(`baiduboxapp://ugc/isShowProgressView?upgrade=1&params=${encodeURIComponent(JSON.stringify(params))}`);
    }

    obIns = null;
    createIntersectionObserver() {
        if (this.obIns) {
            return;
        }
        this.obIns = new IntersectionObserver((entries) => {
            entries.forEach(entry => {
                // 获取到当前dom和className
                const curDom = entry.target;
                const className = curDom.classList[0];
                const ratio = entry.intersectionRatio;
                let value = '';
                className === 'partMethod' && (value = 'rule_publish');
                className === 'activityReward' && (value = 'rule_award');
                className === 'listCard' && (value = 'weekly_board');
                if (ratio > 0.5) {
                    // 打点
                    // console.log(value);
                    sendShowLog({
                        value,
                    });
                    this.obIns.unobserve(curDom);
                }
            })
        }, {
            threshold: [
                0,
                0.5,
                1
            ],
        })
    }

    inited() {
        // 升级页-手白｜版本小于13.28
        if (env.isBoxSeries && versionCompare(env.boxVersion, '13.28') < 0) {
            window.location.href = pageURL.UPDATE;
        };
    }
    attached() {
        // 打点
        this.createIntersectionObserver();
        this.obIns.observe(document.getElementById('partMethod'));
        this.obIns.observe(document.getElementById('activityReward'));
        this.obIns.observe(document.getElementById('listCard'));
        // 首页数据
        this.getData();
        // 监听页面进入
        watchPageState(() => {
            return getData().then(({data}) => {
                const resData = data?.data || {};
                // this.data.set('resData', resData);
                this.data.set('isLogin', resData.is_login);
                const isSucPublish = this.data.get('isSucPublish');
                if (isSucPublish) {
                    setTimeout(() => {
                        this.data.set('isSucPublish', false);
                    }, 7000);
                }
            });
        });
        // 页面打点
        sendShowLog({value: 'essay_activity'});
        // 初始化分享
        this.initShareData();
        // this.profileugcstatus();
        // 取消发布成功toast
        this.setPubToast();

        // 发布器消息通道｜即时广播
        window.getChannel = (_action, res) => {
            // 消息通道返回
            const resObj = JSON.parse(res) || {};
            // 是否展示过loading
            // const isHasShowLoad = this.data.get('isHasShowLoad');
            // 发布进度
            this.data.set('percentage', resObj.percentage);
            // this.data.set('message', resObj);
            // console.log(resObj);
            const isSucPublish = this.data.get('isSucPublish');
            !isSucPublish && this.data.set('isSucPublish', true);
            // 如果上限不在走奖励逻辑
            // const resDataActivity = this.data.get('resDataActivity') || {};
            // const progressArr = resDataActivity?.task?.progress || [];
            // if (progressArr[progressArr.length - 1]?.status) {
            //     return;
            // };

            // "0":失败、"1":等待、"2":运行、"3":成功、"4":删除
            if (resObj.state === "0") {
                // this.data.set('isShowLoadPop', 0);
                // 发布失败或者异常
                setTimeout(() => {
                    toast('发布失败，请稍后重试');
                }, 1000);
                return;
            };

            // 进行中
            // if (+resObj.percentage <= 100 && !isHasShowLoad) {
            //     // this.data.set('isShowLoadPop', 1);
            //     // 设置下进度
            //     const elemBar = document.getElementById("myBar");
            //     elemBar.style.width = (+resObj.percentage) + '%';
            // };

            // 成功&进度100%
            // if (+resObj.percentage === 100 && +resObj.state === 3) {
                // this.data.set('isShowLoadPop', 0);
                // 发布完成后弹出去看看，点击跳转我的视频（我的资产）
                // toast({
                //     button: '去看看',
                //     text:'恭喜您成功发布!',
                //     duration: 7,
                //     onClick() {
                //         invokeScheme("baiduboxapp://v1/easybrowse/open?append=1&backlocation=0&bannerswitch=1&forbidautorotate=1&newbrowser=1&params=%7B%22showtoolbar%22%3A%221%22%7D&type=immerse&url=https%3A%2F%2Fmbd.baidu.com%2Fnewspage%2Fdata%2Fexampublish%3Faction%3Dwallet&zoomswitch=0");
                //     },
                // });
            // };
            // 发布完成
            const publishResult = resObj?.publishResult || {};
            // "0"：失败 "1"：等待 "2"：运行 "3"：成功 "4"：删除
            if (publishResult?.params?.nid) {
                toast({
                    button: '去看看',
                    text:'恭喜您成功发布!',
                    duration: 7,
                    onClick() {
                        invokeScheme("baiduboxapp://v1/easybrowse/open?append=1&backlocation=0&bannerswitch=1&forbidautorotate=1&newbrowser=1&params=%7B%22showtoolbar%22%3A%221%22%7D&type=immerse&url=https%3A%2F%2Fmbd.baidu.com%2Fnewspage%2Fdata%2Fexampublish%3Faction%3Dwallet&zoomswitch=0");
                    },
                });
                const publishNid = publishResult?.params?.nid;
                const requestedList = this.data.get('requestedList');
                if (!requestedList.includes(publishNid)) {
                    // 去重，请求一次
                    this.data.push('requestedList', publishNid);
                    // 请求成功弹窗
                    fly.post(`/newspage/data/exampublish`, {
                        nid: `${publishResult?.params?.nid}`
                    }, {
                        headers: {
                            "content-type": "application/x-www-form-urlencoded"
                        }
                    }).then(({data}) => {
                        const responsedata = data?.data || {};
                        if (!responsedata) {
                            return;
                        };
                        this.data.set('isLogin', responsedata?.is_login);
                        // this.data.set('sucpopResData', responsedata);
                    });
                };
            };
        };

        // 发布器scheme回调
        window.gk_video_callback = (data = {}) => {
            data = JSON.parse(data);
            // status: 0成功｜1取消、2失败、3登录失败
            // https://ku.baidu-int.com/knowledge/HFVrC7hq1Q/qCCPuIpbfn/yZRTvK33Xc/IjgMVZu7miGn6p
            if (data?.status === '0' || data?.params?.taskId) {
                this.profileugcstatus();
                // 上限toast提示
                // const resDataActivity = this.data.get('resDataActivity') || {};
                // const progressArr = resDataActivity?.task?.progress || [];
                // if (progressArr[progressArr.length - 1]?.status) {
                //     const isHasShowTip = this.data.get('isHasShowTip');
                //     if (!isHasShowTip) {
                //         this.data.set('isShowLoadPop', 0);
                //         this.data.set('isHasShowTip', 1);
                //         boxx.call('commonUI.showToast', {
                //             type: '1',
                //             message: '今日红包已领完，请明日再来'
                //         });
                //     };
                // };
                // // 重置toast状态
                // this.data.set('isHasShowTip', 0);
            };
        };
        
        // 发布器进度回调｜只一次
        window.getOutboxShowDataCB = () => {};

        // 初始化消息通道
        this.profileugcstatus();
    }

    detached() {
        // 注销消息通道
        datachannelUnRegister();
        if (this.obIns.length) {
            this.obIns.forEach(item => item.disconnect());
        }
    }

    getData() {
        return getData().then(({data}) => {
            const resData = data?.data || {};
            if (!resData) {
                return;
            }

            // 尝试处理首页数据
            try {
                this.actions.setPageInit(true);
                this.data.set('resData', resData);
                // console.log(resData);
                this.data.set('resDataActivity', resData.activity);
                this.data.set('isLogin', resData.is_login);
                // this.data.set('isFinishInfo', resData.is_finish_info);
                // 活动状态
                this.setActivity();
                // resData.activity_status = 0;
                this.data.set('isShowHome', +resData.activity_status === 0);
            }
            catch (e) {
                // 页面初始化失败
                this.actions.setPageInit(false);
                boxx.call('commonUI.showToast', {
                    type: '1',
                    message: '数据异常，请稍后重试'
                });
            }
        });
    }

    initData() {
        return {
            resData: {},
            resDataActivity: {},
            isLogin: 0,
            percentage: 0,
            requestedList: [],
            isShowHome: 0,
            firstFingerClick: true,
            isSucPublish: false,
        };
    }
}

connect.san(
    {
        pageInit: 'pageInit',
        shareInfo: 'shareInfo',
    },
    {
        setPageInit: 'setPageInit',
        setShareInfo: 'setShareInfo',
    }
)(App);
