lor: rgba(0, 0, 0, 0.01);\n  opacity: 0.3;\n}\n.s-navbar .hairline,\n.s-navbar .hairline:after {\n  border-bottom: 1px solid;\n  border-color: rgba(238, 238, 238, 1);\n}\n.darkmode .s-navbar .hairline,\n.darkmode .s-navbar .hairline:after {\n  border-color: rgba(0, 0, 0, 0);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 150%;\n    height: 150%;\n    -webkit-transform: scale(0.66666667);\n            transform: scale(0.66666667);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 200%;\n    height: 200%;\n    -webkit-transform: scale(0.5);\n            transform: scale(0.5);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 300%;\n    height: 300%;\n    -webkit-transform: scale(0.33333333);\n            transform: scale(0.33333333);\n    -webkit-transform-origin: 0 0;\n            transform-origin: 0 0;\n  }\n}\n.s-navbar .s-icon {\n  display: block;\n}\n.s-navbar .btn-icon,\n.s-navbar .btn-text {\n  display: -webkit-flex;\n  display: flex;\n  -webkit-flex-direction: row;\n          flex-direction: row;\n  -webkit-align-items: center;\n          align-items: center;\n  -webkit-justify-content: center;\n          justify-content: center;\n  height: 100%;\n}\n.s-navbar .btn-icon:active,\n.s-navbar .btn-text:active {\n  opacity: 0.2;\n}\n.s-navbar .btn-icon {\n  width: 0.44rem;\n}\n.s-navbar .btn-text {\n  padding: 0 0.07rem;\n  font-size: 0.16rem;\n}\n", "",{"version":3,"sources":["index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/text/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/navbar/style/index.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/style/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/wuji-uikit/src/color/mixins.less","/Users/liaohuanyu/projects/baidu-work/baidu/feed3/multi-pages/node_modules/@baidu/nano/es/theme/_theme.less"],"names":[],"mappings":"AAAA;;;EAGE;AACF;;EAEE;AACF;;;EAGE;AACF;;;EAGE;AACF;;;EAGE;AACF;;EAEE;AACF;;;;EAIE;AC1BD;ECYG,kBAAA;EACA,WAAA;EACA,eAAA;AFiBJ;AEfI;ECuJA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;ECvCA,0BAAA;EApHA,wCAAA;EFKI,WAAA;EACA,eAAA;AFoBR;ACzCC;EG0Ie,+BAAA;AJ9FhB;AC5CC;EGsBe,qCAAA;AJyBhB;AEvBI;EACI,eAAA;EACA,MAAA;EACA,gBAAA;AFyBR;AEtBI;EACI,wBAAA;EACA,gBAAA;EACA,MAAA;AFwBR;AErBI;EAEI,kBAAA;EACA,iBAAA;AFsBR;AEnBI;;EC6HA,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;ECvCA,0BAAA;EFrFI,kBAAA;EACA,YAAA;AFwBR;ACvEC;;EG0Ie,+BAAA;AJ/DhB;AEzBI;EACI,YAAA;EACA,eAAA;AF2BR;AExBI;EACI,aAAA;AF0BR;AClFC;EImIG,kBAAA;EHtEI,kBAAA;EACA,OAAA;EACA,QAAA;EACA,SAAA;EACA,WAAA;AFyBR;AC1FC;EIgEO,gCAAA;EACA,qCAAA;EACA,YAAA;AL6BR;AKuCI;;EAEI,wBAAA;EDhEJ,oCAAA;AJ4BJ;ACpGC;;EG+Ee,8BAAA;AJyBhB;AKoCI;EAAA;IApEA,YAAA;ELoCF;EKlCE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,oCAAA;YAAA,4BAAA;IACA,6BAAA;YAAA,qBAAA;ELoCN;AACF;AK2BI;EAAA;IA3EA,YAAA;ELoDF;EKlDE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,6BAAA;YAAA,qBAAA;IACA,6BAAA;YAAA,qBAAA;ELoDN;AACF;AKgBI;EAAA;IAhFA,YAAA;ELoEF;EKlEE;IACI,WAAA;IACA,kBAAA;IACA,MAAA;IACA,OAAA;IACA,oBAAA;IACA,WAAA;IACA,YAAA;IACA,oCAAA;YAAA,4BAAA;IACA,6BAAA;YAAA,qBAAA;ELoEN;AACF;ACxJC;ECqEO,cAAA;AFsFR;AC3JC;;EEuKG,qBAAA;EAAA,aAAA;EACA,2BAAA;UAAA,mBAAA;EACA,2BAAA;UAAA,mBAAA;EACA,+BAAA;UAAA,uBAAA;ED/FI,YAAA;AFwFR;AEtFQ;;EACI,YAAA;AFyFZ;ACvKC;ECmFO,cAAA;AFuFR;AC1KC;ECuFO,kBAAA;EACA,kBAAA;AFsFR","file":"index.less","sourcesContent":["/**\n * @file 组件样式 导航栏\n * @author baozhixin <baozhixin@baidu.com>\n */\n/**\n * core\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * @file 无极元素库 index.less\n * @author wuji.baidu-int.com\n */\n/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n/**\n * variables 变量声明\n */\n/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n.s-navbar {\n  position: relative;\n  z-index: 90;\n  height: 132pr;\n}\n.s-navbar-body {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  color: rgba(30, 31, 36, 1);\n  background-color: rgba(255, 255, 255, 1);\n  width: 100%;\n  height: 132pr;\n}\n.darkmode .s-navbar-body {\n  color: rgba(255, 255, 255, 0.8);\n}\n.darkmode .s-navbar-body {\n  background-color: rgba(27, 27, 27, 1);\n}\n.s-navbar-fixed {\n  position: fixed;\n  top: 0;\n  max-width: 750px;\n}\n.s-navbar-sticky {\n  position: -webkit-sticky;\n  position: sticky;\n  top: 0;\n}\n.s-navbar-title {\n  font-size: 54pr;\n  font-weight: bold;\n}\n.s-navbar-aside,\n.s-navbar-tools {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  color: rgba(80, 82, 92, 1);\n  position: absolute;\n  height: 100%;\n}\n.darkmode .s-navbar-aside,\n.darkmode .s-navbar-tools {\n  color: rgba(255, 255, 255, 0.8);\n}\n.s-navbar-aside {\n  left: 30pr;\n  max-width: 100%;\n}\n.s-navbar-tools {\n  right: 30pr;\n}\n.s-navbar .hairline {\n  position: relative;\n  position: absolute;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  height: 1px;\n}\n.nightmode .s-navbar .hairline:after {\n  border-color: rgba(0, 0, 0, 0.9);\n  background-color: rgba(0, 0, 0, 0.01);\n  opacity: 0.3;\n}\n.s-navbar .hairline,\n.s-navbar .hairline:after {\n  border-bottom: 1px solid;\n  border-color: rgba(238, 238, 238, 1);\n}\n.darkmode .s-navbar .hairline,\n.darkmode .s-navbar .hairline:after {\n  border-color: rgba(0, 0, 0, 0);\n}\n@media (-webkit-min-device-pixel-ratio: 1.2), (-webkit-max-device-pixel-ratio: 1.5), (min-resolution: 1.2dppx), (max-resolution: 1.5dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 150%;\n    height: 150%;\n    transform: scale(0.66666667);\n    transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 200%;\n    height: 200%;\n    transform: scale(0.5);\n    transform-origin: 0 0;\n  }\n}\n@media (-webkit-min-device-pixel-ratio: 3), (min-resolution: 3dppx) {\n  .s-navbar .hairline {\n    border: none;\n  }\n  .s-navbar .hairline:after {\n    content: '';\n    position: absolute;\n    top: 0;\n    left: 0;\n    pointer-events: none;\n    width: 300%;\n    height: 300%;\n    transform: scale(0.33333333);\n    transform-origin: 0 0;\n  }\n}\n.s-navbar .s-icon {\n  display: block;\n}\n.s-navbar .btn-icon,\n.s-navbar .btn-text {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n}\n.s-navbar .btn-icon:active,\n.s-navbar .btn-text:active {\n  opacity: 0.2;\n}\n.s-navbar .btn-icon {\n  width: 132pr;\n}\n.s-navbar .btn-text {\n  padding: 0 21pr;\n  font-size: 48pr;\n}\n","/**\n * @file text mixins\n * @author Sheeta(wuhayao@gmail.com)\n */\n\n.wuji-text(@code, @line-spacing) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font(@t-code, @line-spacing, @f-code);\n    .wuji-color(@cam-code);\n}\n\n.wuji-text-one-line(@code, @line-height: 1) {\n    @t-code: #Txt_T[@@code];\n    @f-code: #Txt_F[@@code];\n    @cam-code: #Txt_CAM[@@code];\n\n    .wuji-font-one-line(@t-code, @line-height: @line-height, @f-code);\n    .wuji-color(@cam-code);\n}\n","/**\n * @file 组件样式 导航栏\n * @author baozhixin <baozhixin@baidu.com>\n */\n\n@import '../../style/index.less';\n\n@icon-cls: s-icon;\n@prefix-cls: s-navbar;\n@navbar-height: 132pr;\n\n.@{prefix-cls} {\n    position: relative;\n    z-index: 90;\n    height: @navbar-height;\n\n    &-body {\n        .centerfy();\n        .theme-color(GC1);\n        .theme-background(GC18);\n        width: 100%;\n        height: @navbar-height;\n    }\n\n    &-fixed {\n        position: fixed;\n        top: 0;\n        max-width: 750px;\n    }\n\n    &-sticky {\n        position: -webkit-sticky;\n        position: sticky;\n        top: 0;\n    }\n\n    &-title {\n        // .theme-color(GC1); // mark: inherit\n        font-size: 54pr;\n        font-weight: bold;\n    }\n\n    &-aside,\n    &-tools {\n        .centerfy();\n        .theme-color(BC6);\n        position: absolute;\n        height: 100%;\n    }\n\n    &-aside {\n        left: 30pr;\n        max-width: 100%;\n    }\n\n    &-tools {\n        right: 30pr;\n    }\n\n    .hairline {\n        .theme-hairline(bottom, GC35);\n        position: absolute;\n        left: 0;\n        right: 0;\n        bottom: 0;\n        height: 1px;\n    }\n\n    .@{icon-cls} {\n        display: block;\n    }\n\n    .btn-icon,\n    .btn-text {\n        .centerfy();\n        height: 100%;\n\n        &:active {\n            opacity: .2;\n        }\n    }\n\n    .btn-icon {\n        width: 132pr;\n    }\n\n    .btn-text {\n        padding: 0 21pr;\n        font-size: @T5;\n    }\n}\n","/**\n * mixins\n *\n * 注意：部分mixin依赖theme/_theme.less\n */\n\n// 背景图\n.coverimg() {\n    background-position: 50% 50%;\n    background-repeat: no-repeat;\n    background-size: cover;\n}\n\n// 全局字号调整\n.font-size-change() {\n    .font-scale(@font-size, @line-height, @font-scale: 1) {\n        font-size: @font-size / @font-base * @font-scale;\n        line-height: @line-height / @font-base * @font-scale;\n    }\n\n    .font-size-0 & {\n        .font-scale(42px, 66px);\n    }\n    .font-size-1 & {\n        .font-scale(51px, 78px);\n    }\n    .font-size-2 & {\n        .font-scale(57px, 78px);\n    }\n    .font-size-3 & {\n        .font-scale(63px, 84px);\n    }\n\n    @media screen and (max-width: 360px) {\n        .font-size-0 & {\n            .font-scale(42px, 66px, 0.9);\n        }\n        .font-size-1 & {\n            .font-scale(51px, 78px, 0.9);\n        }\n        .font-size-2 & {\n            .font-scale(57px, 78px, 0.9);\n        }\n        .font-size-3 & {\n            .font-scale(63px, 84px, 0.9);\n        }\n    }\n}\n\n// 夜间模式替换背景图\n.nightmode(@icon, @flag: true, @join: false) when (@flag = false) {\n    background-image: url('@{icon-path}/@{icon}');\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = false) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    @{selector-night} & {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n.nightmode(@icon, @flag: true, @join: false) when (@flag = true) and (@join = true) {\n    background-image: url('@{icon-path}/@{icon}');\n\n    .nightmode& {\n        background-image: url('@{icon-path}/nightmode-@{icon}');\n    }\n}\n\n// 熊掌占位图\n.placeholder() {\n    .theme-image('bd-logo.png');\n}\n\n// mixin: 复合属性radius不同media下的运算\n.calc-radius(@radius: 0, @ratio: 1) {\n    each(@radius, {\n        border-radius+_: extract(@radius, @key) * @ratio;\n    });\n}\n\n// 1px 边框说明: position有效值为 [top, right, bottom, left, around]; position为around 时圆角 radius 有效显示四条边\n.hairline(@position: bottom, @color: #000, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border: 1px solid @color;\n    border-radius: @radius;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid @color;\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.hairline(@position: bottom, @color: #000) when not (@position = around) {\n    position: relative;\n    border: 1px solid transparent;\n    border-@{position}-color: @color;\n    .hairline-nightmode();\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            border: 1px solid transparent;\n            border-@{position}-color: @color;\n        }\n    }\n}\n\n// 绝对定位或固定定位填充满父元素或祖父元素\n.fullfill(@position: absolute) {\n    position: @position;\n    left: 0;\n    top: 0;\n    width: 100%;\n    height: 100%;\n}\n\n// 容器内容居中\n.centerfy(@direction: row) {\n    display: flex;\n    flex-direction: @direction;\n    align-items: center;\n    justify-content: center;\n}\n\n// 多行截断\n.line-clamp(@line: 2) {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    display: -webkit-box;\n    -webkit-line-clamp: @line;\n    -webkit-box-orient: vertical;\n    word-break: break-all;\n}\n\n// 单行截断\n.ellipsis() {\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    min-width: 0;\n}\n\n// 清除浮动\n.clearfix() {\n    &:after {\n        content: '';\n        display: table;\n        clear: both;\n    }\n}\n\n// 生成纯色填充的svg背景图\n.svg-background(@color: #fff) {\n    @svg: escape('<svg xmlns=\"http://www.w3.org/2000/svg\"><rect fill=\"@{color}\" width=\"100%\" height=\"100%\"/></svg>');\n    background-image: url('data:image/svg+xml;charset=utf8,@{svg}');\n}\n","/**\n * @file color mixins\n * @author wuji.baidu-int.com\n *\n * 区别于color、background-color、border-color等受全局dark-mode和night-mode变量影响。\n * fill属性可以定义局部变量night-mode，这在关闭夜间模式情况下，可以单独保留svg填充颜色fill属性。\n * 究其原因：百度APP安卓夜间模式下，基本的颜色属性不能通过css定义生效，svg的颜色属性例外。建议自行测试验证和理解。\n */\n\n// TODO: 使用css变量\n// .use-cssvar() {}\n\n// mixin: 生成背景颜色\n.wuji-background-color(@code: undefined) {\n    @color-default: getColor(@code);\n    background-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                background-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                background-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            background-color: @color-default;\n        }\n    }\n}\n\n// mixin: 添加区分模式的背景图片(可置于全局mixin中)\n.wuji-background-image(@src: null, @src-dark: null, @src-night: null) {\n    & when not (@src = null) {\n        background-image: url(@src);\n    }\n    & when (@multi-mode = true) {\n        & when not (@src-dark = null) and (@dark-mode = true) and not (@src = @src-dark) {\n            @{selector-dark} & {\n                background-image: url(@src-dark);\n            }\n        }\n        & when not (@src-night = null) and (@night-mode = true) and not (@src = @src-night) {\n            @{selector-night} & {\n                background-image: url(@src-night);\n            }\n        }\n    }\n}\n\n// mixin: 生成背景颜色\n.wuji-background(@code: undefined) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成描边颜色\n.wuji-border-color(@code: undefined, @position: around) {\n    @color-default: getColor(@code);\n    @prop: if(@position = around, e('border-color'), e('border-@{position}-color'));\n    @{prop}: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                @{prop}: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                @{prop}: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            @{prop}: @color-default;\n        }\n    }\n}\n\n// mixin: 生成描边颜色\n// 当第一个参数是多值情况且固定顺序 <line-width> <line-style> <color> 时\n// 会调用line/mixin.less中的wuji-border方法，参数示例：1px solid red\n.wuji-border(@code: undefined, @position: around) when (length(@code) = 1) {\n    .wuji-border-color(@code, @position);\n}\n\n// mixn: 生成上描边颜色\n.wuji-border-top-color(@code: undefined) {\n    .wuji-border-color(@code, top);\n}\n\n// mixn: 生成下描边颜色\n.wuji-border-bottom-color(@code: undefined) {\n    .wuji-border-color(@code, bottom);\n}\n\n// mixn: 生成左描边颜色\n.wuji-border-left-color(@code: undefined) {\n    .wuji-border-color(@code, left);\n}\n\n// mixn: 生成右描边颜色\n.wuji-border-right-color(@code: undefined) {\n    .wuji-border-color(@code, right);\n}\n\n// mixin: 生成文字颜色\n.wuji-color(@code: undefined) {\n    @color-default: getColor(@code);\n    color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成svg填充颜色\n.wuji-fill(@code: undefined, @night-mode: true) {\n    @color-default: getColor(@code);\n    fill: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                fill: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                fill: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            fill: @color-default;\n        }\n    }\n}\n\n// mixin: 生成光标颜色\n.wuji-caret(@code: undefined) {\n    @color-default: getColor(@code);\n    caret-color: @color-default;\n\n    & when (@multi-mode = true) and (@dark-mode = true) {\n        @color-dark: getColor(@code, dark);\n\n        & when (@force-dark-output = true), not (@color-dark = 'null') and not (@color-dark = @color-default) {\n            @{selector-dark} & {\n                caret-color: @color-dark;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and (@night-mode = true) {\n        @color-night: getColor(@code, night);\n\n        & when not (@color-night = 'null') and not (@color-night = @color-default) {\n            @{selector-night} & {\n                caret-color: @color-night;\n            }\n        }\n    }\n\n    & when (@multi-mode = true) and not (@selector-default = '') {\n        @{selector-default} & {\n            caret-color: @color-default;\n        }\n    }\n}\n\n// mixin: 生成透明度\n.wuji-opacity(@code: undefined) {\n    opacity: getOpacity(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.wuji-root(\n    @color: CAM_X791, // GC1\n    @background: CAM_X392, // GC9\n    @selector-root: @selector-root,\n    @selector-dark: @selector-dark,\n    @selector-night: @selector-night\n) {\n    // 如果选择器参数是字符串类型，需要escape\n    @rule1: if(isstring(@selector-root), e(@selector-root), @selector-root);\n    @rule2: if(isstring(@selector-dark), e(@selector-dark), @selector-dark);\n    @rule3: if(isstring(@selector-night), e(@selector-night), @selector-night);\n\n    @{rule1} {\n        color: getColor(@color);\n        background-color: getColor(@background);\n\n        & when (@multi-mode = true) and (@dark-mode = true) {\n            &@{rule2} {\n                color: getColor(@color, Dark);\n                background-color: getColor(@background, Dark);\n            }\n        }\n\n        & when (@multi-mode = true) and (@night-mode = true) {\n            &@{rule3} {\n                color: getColor(@color, Night);\n                background-color: getColor(@background, Night);\n            }\n        }\n    }\n}\n","/**\n * @file theme 主题\n * @author bdapp-fe<bdapp-fe@baidu.com>\n */\n\n// 是否使用原生CSS变量，默认false\n@use-cssvar: false;\n// 是否支持多种模式：默认模式，暗黑模式，夜间模式；false 时只支持默认模式\n@multi-mode: true;\n// 是否支持暗黑模式, @multi-mode 为 true 时有效\n@dark-mode: true;\n// 是否支持夜间模式, @multi-mode 为 true 时有效\n@night-mode: false;\n// 主题颜色全局选择器，定义在根元素上，默认根元素html，可通过赋值改变\n@selector-root: html;\n// 日间模式 类选择器 可用于暗黑模式下部分区域依旧需要保持日间模式的情况\n@selector-default: '';\n// 深色模式 类选择器\n@selector-dark: .darkmode;\n// 夜间模式 类选择器\n@selector-night: .nightmode;\n\n// 主题图标路径\n@image-path: '../assets/img';\n\n.use-cssvar() when(@use-cssvar = true) {\n}\n\n// mixin: 生成文字颜色\n.theme-color(@code) when (@use-cssvar = true) {\n}\n.theme-color(@code) when (@use-cssvar = false) {\n    .wuji-color(@code);\n}\n\n// mixin: 生成svg填充颜色\n.theme-fill(@code) when (@use-cssvar = true) {\n}\n.theme-fill(@code) when (@use-cssvar = false) {\n    .wuji-fill(@code);\n}\n\n// mixin: 生成背景颜色\n.theme-background(@code) when (@use-cssvar = true) {\n}\n.theme-background(@code) when (@use-cssvar = false) {\n    .wuji-background-color(@code);\n}\n\n// mixin: 生成边框颜色\n.theme-border(@code) when (@use-cssvar = true) {\n}\n.theme-border(@code) when (@use-cssvar = false) {\n    .wuji-border-color(@code);\n}\n\n// mixin: 根元素注入默认color、background-color, 默认根元素html\n.theme-root() {\n    .wuji-root();\n}\n\n// hack: 安卓夜间模式1px看不清，通过在伪元素:after上定义rgba颜色后再微调透明度\n.hairline-nightmode() {\n    @{selector-night} &:after {\n        border-color: rgba(0,0,0,0.9);\n        background-color: rgba(0,0,0,0.01);\n        opacity: .3;\n    }\n}\n\n// 根据media query缩放，dpr: 1.5、2、3\n.hairline-media(@dpr, @position) {\n    border: none;\n\n    &:after {\n        content: '';\n        position: absolute;\n        top: 0;\n        left: 0;\n        pointer-events: none;\n        width: @dpr * 100%;\n        height: @dpr * 100%;\n        transform: scale(1 / @dpr);\n        transform-origin: 0 0;\n    }\n}\n\n// mixin: 使用色表编号的1px边框，不支持自定义颜色，自定义颜色可使用.hairline(...)\n.theme-hairline(@position, @code: GC34, @radius: 0) when (@position = around) {\n    position: relative;\n    overflow: visible;\n    border-radius: @radius;\n    .theme-border(@code);\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n        &:after {\n            .calc-radius(@radius, 1.5);\n            // border-radius: @radius * 1.5;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n        &:after {\n            .calc-radius(@radius, 2);\n            // border-radius: @radius * 2;\n        }\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n        &:after {\n            .calc-radius(@radius, 3);\n            // border-radius: @radius * 3;\n        }\n    }\n}\n.theme-hairline(@position: bottom, @code: GC34) when not (@position = around) {\n    position: relative;\n    .hairline-nightmode();\n\n    &,\n    &:after {\n        border-@{position}: 1px solid;\n        .theme-border(@code);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 1.2),\n        (-webkit-max-device-pixel-ratio: 1.5),\n        (min-resolution: 1.2dppx),\n        (max-resolution: 1.5dppx) {\n        .hairline-media(1.5, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 2),\n        (min-resolution: 2dppx) {\n        .hairline-media(2, @position);\n    }\n\n    @media (-webkit-min-device-pixel-ratio: 3),\n        (min-resolution: 3dppx) {\n        .hairline-media(3, @position);\n    }\n}\n\n// mixin: 不同主题下背景图路径\n.theme-image(@image, @light: true, @night: true, @dark: true, @base64: false) {\n    @output4light: if(@light = true, {\n        @img: '@{image-path}/@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        background-image: @url;\n    }, {});\n\n    @output4dark: if(@dark = true, {\n        @img: '@{image-path}/darkmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-dark} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4night: if(@night = true, {\n        @img: '@{image-path}/nightmode-@{image}';\n        @url: if(@base64 = true, data-uri(@img), url(@img));\n        @{selector-night} & {\n            background-image: @url;\n        }\n    }, {});\n\n    @output4light();\n\n    @output4dark();\n\n    @output4night();\n}\n"]}]);
